import { Injectable } from "@angular/core";
import {
	ICompetenceTypeModel,
	ICompetenceTypeRequest,
} from "app/org-management-setup/models/competence-type";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class CompetenceTypeService extends AppClientService {
	constructor() {
		super();
	}

	create(data: ICompetenceTypeRequest) {
		return this.post<IResponse<ICompetenceTypeModel>>("Competencetype/Create", data);
	}

	update(data: ICompetenceTypeRequest) {
		return this.post<IResponse<ICompetenceTypeModel>>("CompetenceType/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<ICompetenceTypeModel[]>>("CompetenceType/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<ICompetenceTypeModel[]>>("CompetenceType/GetAll");
	}

	Delete(Id: number) {
		return this.delete<IResponse>("CompetenceType/Delete", {
			params: { Id: Id },
		});
	}
}
