import { TranslateService } from "@ngx-translate/core";
import { Injectable, Injector, inject } from "@angular/core";
import { StyleModel, arabicStylesList, enStylesList } from "../resources/styles-path";
import { StringsManager } from "../resources/strings-manager";
import { Storage } from "../resources/local-storage";
import { Constants } from "../resources/constants";
import { Languages } from "../enums/languages.enum";

@Injectable({
	providedIn: "root",
})
export class LanguageService {
	currentLang: string;
	isArabic: boolean;
	isEnglish: boolean;
	arStyles: StyleModel[];
	enStyles: StyleModel[];
	head: HTMLHeadElement;
	public appStrings: StringsManager = new StringsManager();
	public translateService = inject(TranslateService);

	constructor() {
		this.head = document.head;
		this.arStyles = arabicStylesList;
		this.enStyles = enStylesList;
		this.currentLang = Storage.getLocalLang;
	}

	changeLanguage(language: string) {
		this.translateService.use(language ?? Constants.defaulLang);
		Storage.setLocalLang(language ?? Constants.defaulLang);

		this.currentLang = language;
		if (this.currentLang == Languages.ar) {
			this.isArabic = true;
			this.isEnglish = false;
		} else {
			this.isEnglish = true;
			this.isArabic = false;
		}

		this.changeStyle(language);
	}

	translate(key: string): string {
		let msg;
		this.translateService.get(key).subscribe((val) => {
			msg = val;
		});
		return msg;
	}

	changeStyle(currentLang) {
		// const head = document.getElementsByTagName("head")[0];
		if (currentLang == Languages.ar) {
			// add ar style
			this.arStyles.forEach((style) => {
				let ele = document.createElement("link");
				ele.id = style.id;
				ele.rel = style.rel;
				ele.href = style.href;
				this.head.appendChild(ele);
			});

			// remove en style
			this.enStyles.forEach((style) => {
				document.getElementById(style.id)?.remove();
			});
		} else {
			// add en style
			this.enStyles.forEach((style) => {
				let ele = document.createElement("link");
				ele.id = style.id;
				ele.rel = style.rel;
				ele.href = style.href;
				this.head.appendChild(ele);
			});

			// remove ar style
			this.arStyles.forEach((style) => {
				document.getElementById(style.id)?.remove();
			});
		}
	}
}
