import { Component, OnInit, Input } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
	IJobTrainingPathRequest,
	IJobTrainingPathModel,
	IJobTrainingPathDeleteRequest,
	IJobTrainingPathPriorityModel,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { JobTrainingPathService } from "app/org-management/services/job-profile/job-training-path.service";
import { CustomValidators } from "ngx-custom-validators";
import { MatTableDataSource } from "@angular/material/table";
import { ICourseModel } from "app/training/models/course";
import { CourseService } from "app/training/services/course.service";

@Component({
	selector: "app-job-training-path",
	templateUrl: "./job-training-path.component.html",
	styleUrls: ["./job-training-path.component.scss"],
})
export class JobTrainingPathComponent extends AppHelpers implements OnInit {
	@Input() jobId: number;

	form: FormGroup;
	submitted: boolean = false;

	mandatoryColomns: string[] = ["Ser", "course", "period", "pre_Required", "priority", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	dataSource: MatTableDataSource<IJobTrainingPathModel>;

	courseList: ICourseModel[];
	priorityList: IJobTrainingPathPriorityModel[];

	constructor(
		private jobTrainingPathService: JobTrainingPathService,
		private courseService: CourseService
	) {
		super();
	}

	ngOnInit() {
		this.form = this.fb.group({
			course_ID: [null, [Validators.required]],
			period: [
				null,
				[CustomValidators.digits, CustomValidators.gte(0), CustomValidators.lte(2147483647)],
			],
			pre_Required: [null],
			priority_ID: [null],
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllCourses();
			this.getAllPriorities();
			if (this.jobId > 0) this.getAll();
		}
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
		this.jobTrainingPathService
			.getAllByJobTitleId(this.jobId)
			.subscribe((res: IResponse<IJobTrainingPathModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((item: IJobTrainingPathModel) => {
						item.course = this.msgTranslate(item.trA_Course?.ar_Name, item.trA_Course?.en_Name);
						item.priority = this.msgTranslate(
							item.comm_Priority?.ar_Name,
							item.comm_Priority?.en_Name
						);
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IJobTrainingPathRequest = {
				joB_Title_ID: this.jobId,
				course_ID: this.form.value.course_ID,
				period: this.form.value.period,
				pre_Required: this.form.value.pre_Required ?? false,
				priority_ID: this.form.value.priority_ID,
			};

			this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IJobTrainingPathRequest) {
		this.showLoading();
		this.jobTrainingPathService.create(model).subscribe((res: IResponse<IJobTrainingPathModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	onDelete(jobTrainingPath: IJobTrainingPathModel): void {
		let msg = this.msgTranslate(jobTrainingPath.course);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(jobTrainingPath);
		});
	}

	delete(jobTrainingPath: IJobTrainingPathModel) {
		let params: IJobTrainingPathDeleteRequest = {
			CourseId: jobTrainingPath.course_ID,
			JobId: jobTrainingPath.joB_Title_ID,
		};
		this.showLoading();
		this.jobTrainingPathService.Delete(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	// course List
	getAllCourses() {
		this.showLoading();
		this.courseService.getAll().subscribe((res) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.courseList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	// priority List
	getAllPriorities() {
		this.showLoading();
		this.jobTrainingPathService.getAllPriority().subscribe((res) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.priorityList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
