import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IReportsModel } from "app/general-setup/models/iso-code";
import { IsoCodeService } from "app/general-setup/services/iso-code.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "reports-dropdown",
	templateUrl: "./reports-dropdown.component.html",
	styleUrls: ["./reports-dropdown.component.scss"],
})
export class ReportsDrpdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = true;
	@Input() multiple: boolean = false;
	@Input() filter: boolean = true;
	@Input() label: string = "reports";
	@Input() isoChanged: boolean;
	@Output() select = new EventEmitter<IReportsModel>();
	reportsList: IReportsModel[];
	constructor(private _reportNameService: IsoCodeService) {
		super();
	}

	ngOnInit(): void {
		if (this.checkNoBusinessOrCompany()) this.GetAllReports();
	}
	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.GetAllReports();
	}

	ngOnChanges(changes: SimpleChanges): void {
		this.GetAllReports();
	}

	GetAllReports() {
		this._reportNameService.GetAllReports().subscribe((res: IResponse<IReportsModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.reportsList = res.data;
			} else this.errorList(res.message?.messages);
			this.hideLoading();
		});
	}

	onSelect($event: IReportsModel[]) {
		if ($event?.length > 0) this.select.emit($event[0]);
		else this.select.emit(null);
	}
}
