import { Component, Injector, Input, OnInit } from "@angular/core";
import { AppHelpers, Page } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { SortEvent } from "primeng/api";
import { Table } from "primeng/table";

@Component({
	selector: "app-primeng-table",
	templateUrl: "./primrng-table.component.html",
	styleUrls: ["./primrng-table.component.scss"],
})
export class PrimengTableComponent implements OnInit {
	@Input() cols: any;
	@Input() list: any[];
	page: Page = new Page();

	PageSizeOptions = Constants.pageSizeOptions;

	ngOnInit(): void {}

	customSort(event: SortEvent) {
		event.data.sort((data1, data2) => {
			let value1 = data1[event.field];
			let value2 = data2[event.field];
			let result = null;

			if (value1 == null && value2 != null) result = -1;
			else if (value1 != null && value2 == null) result = 1;
			else if (value1 == null && value2 == null) result = 0;
			else if (typeof value1 === "string" && typeof value2 === "string")
				result = value1.localeCompare(value2);
			else result = value1 < value2 ? -1 : value1 > value2 ? 1 : 0;

			return event.order * result;
		});
	}

	clear(table: Table) {
		table.clear();
	}
}
