import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IS4SystemInstanceMethodModel,
  IS4SystemInstanceMethodRequest,
} from "../models/s4-system-instance";

@Injectable({
  providedIn: "root",
})
export class S4SystemInstanceMethodsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IS4SystemInstanceMethodRequest) {
    return this.post<IResponse<IS4SystemInstanceMethodModel>>(
      "SystemInstanceS4Setup/Create_Methods_S4",
      data
    );
  }

  update(data: IS4SystemInstanceMethodRequest) {
    return this.post<IResponse<IS4SystemInstanceMethodModel>>(
      "SystemInstanceS4Setup/Edit_Methods_S4",
      data
    );
  }

  getAllBySystemInstanceId(systemInstanceId: number) {
    return this.get<IResponse<IS4SystemInstanceMethodModel[]>>(
      "SystemInstanceS4Setup/GetAll_Methods_S4",
      {
        params: {
          $filter: `s4_instance_Id eq ${systemInstanceId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SystemInstanceS4Setup/Delete_Methods_S4", {
      params: { Id: Id },
    });
  }
}
