import { AbstractControl, FormGroup, ValidationErrors, ValidatorFn } from "@angular/forms";
import * as moment from "moment";
import { Constants } from "../resources/constants";
export class CustomValidator {
	// No White Space
	static whiteSpace(control: AbstractControl): ValidationErrors | null {
		if (((control.value as string) || "").trim().length === 0) {
			return { whiteSpace: true };
		}
		return null;
	}

	static noSpace(control: AbstractControl): ValidationErrors | null {
		if (control.value?.length > 0 && (control.value as string).trim().indexOf(" ") >= 0) {
			return { noSpace: true };
		}
		return null;
	}

	static MobileStartsWith(control: AbstractControl): ValidationErrors | null {
		if (
			control.value?.length > 0 &&
			!(control.value as string).startsWith(Constants.mobileStartsWith)
		) {
			return { MobileStartsWith: true };
		}
		return null;
	}

	static mobileLength(control: AbstractControl): ValidationErrors | null {
		if (control.value?.length != Constants.mobileLength) {
			return { mobileLength: true };
		}
		return null;
	}

	static compareDates(fromDate: string, toDate: string) {
		return (formGroup: FormGroup) => {
			const fromControl = formGroup?.controls[fromDate];
			const toControl = formGroup?.controls[toDate];

			if (toControl.errors! && !toControl.errors?.compareDates) {
				return;
			}
			// set error on toDate
			if (
				toControl.value &&
				new Date(fromControl.value).getTime() > new Date(toControl.value).getTime()
			) {
				toControl.setErrors({ compareDates: true });
			} else {
				toControl.setErrors(null);
			}
			return null;
		};
	}

	// static dateBefore(control: string, date: string | Date) {
	// 	return (formGroup: FormGroup) => {
	// 		const controlVal = formGroup?.controls[control];
	// 		const dates = moment(controlVal.value);
	// 		if (dates.isBefore(date)) {
	// 			controlVal.setErrors({ dateBefore: true });
	// 		} else {
	// 			controlVal.setErrors(null);
	// 		}
	// 		return null;
	// 	};
	// }

	static smallerThanNumbers(from: string, to: string) {
		return (formGroup: FormGroup) => {
			const fromControl = formGroup?.get(from);
			const toControl = formGroup?.get(to);
			let fromVal = Number(fromControl?.value);
			let toVal = Number(toControl?.value);
			if (fromVal >= toVal) {
				toControl?.setErrors({ smallerThanNumbers: true });
			} else {
				toControl?.setErrors(null);
			}
			return null;
		};
	}

	static greaterThan(from: string, to: string) {
		return (formGroup: FormGroup) => {
			const fromControl = formGroup?.get(from);
			const toControl = formGroup?.get(to);
			let fromVal = Number(fromControl?.value);
			let toVal = Number(toControl?.value);
			if (fromVal >= toVal) {
				toControl?.setErrors({ greaterThan: true, value: fromVal });
			} else {
				toControl?.setErrors(null);
			}
			return null;
		};
	}

	static timeBefore(fromTime: string, toTime: string) {
		return (formGroup: FormGroup) => {
			const fromControl = formGroup?.controls[fromTime];
			const toControl = formGroup?.controls[toTime];
			if (toControl.errors! && !toControl.errors?.timeBefore) return;
			// set error on toDate
			if (toControl.value && toControl.value < fromControl.value) {
				fromControl.setErrors({ timeBefore: true, from: fromTime, to: toTime });
			}
			return null;
		};
	}

	static timeAfter(fromTime: string, toTime: string) {
		return (formGroup: FormGroup) => {
			const fromControl = formGroup?.controls[fromTime];
			const toControl = formGroup?.controls[toTime];
			if (toControl.errors! && !toControl.errors?.timeBefore) return;

			// set error on toDate
			if (toControl.value && toControl.value >= fromControl.value) {
				fromControl.setErrors({ timeAfter: true, from: fromTime, to: toTime });
			}
			return null;
		};
	}
}
