import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IBusinessEntityModel } from "../model/business-entity";

@Injectable({
  providedIn: "root",
})
export class BusinessEntityService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IBusinessEntityModel[]>>(
      "BusinessEntity/GetAll"
    );
  }
}
