import { Injectable } from "@angular/core";
import {
  IJobCompetenceDeleteRequest,
  IJobCompetenceModel,
  IJobCompetenceRequest,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class JobCompetencesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobCompetenceRequest) {
    return this.post<IResponse<IJobCompetenceModel>>(
      "OrgManagement/jobtitle/Create_JobTitleCompetence",
      data
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobCompetenceModel[]>>(
      "OrgManagement/jobtitle/GetAll_JobTitleCompetence",
      {
        params: {
          $filter: `joB_Title_ID eq ${jobId}`,
          $expand: `${db.core_Competence_TYPE},${db.core_Competence_Detail}.${db.core_Competence},${db.core_Competence_Detail}.${db.core_Competence_Level}`,
        },
      }
    );
  }

  Delete(params: IJobCompetenceDeleteRequest) {
    return this.delete<IResponse>(
      "OrgManagement/jobtitle/Delete_JobTitleCompetence",
      {
        params: params,
      }
    );
  }
}
