import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IOrgStructureModel,
  IOrgStructureNodeDeleteParams,
  IOrgStructureNodeModel,
  IOrgStructureNodeRequest,
  IOrgStructureAddRequest,
  IOrgStructureEditRequest,
} from "../model/org-structure";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";
import { IJobTitleModel } from "../model/job-title";
import { IOrganizationModel } from "app/org-management-setup/models/organizations";

@Injectable({
  providedIn: "root",
})
export class OrgStructureService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IOrgStructureAddRequest) {
    return this.post<IResponse<IOrgStructureModel>>(
      "OrgStructure/Create",
      data
    );
  }

  update(data: IOrgStructureEditRequest) {
    return this.post<IResponse<IOrgStructureModel>>("OrgStructure/Edit", data);
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IOrgStructureModel[]>>(
      "OrgStructure/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IOrgStructureModel[]>>("OrgStructure/GetAll");
  }

  getById(id: number) {
    return this.get<IResponse<IOrgStructureModel[]>>("OrgStructure/GetAll", {
      params: {
        $filter: `id eq ${id}`,
      },
    });
  }

  getByIdWithHierarchy(id: number) {
    return this.get<IResponse<IOrgStructureModel[]>>(
      "OrgStructure/GetAll_WithHireicky",
      {
        params: {
          $filter: `id eq ${id}`,
          $expand: `${db.Core_Org_Structure_Org}/${db.Core_Org_TYPE},${db.Core_Org_Structure_Org}/${db.core_Org}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("OrgStructure/Delete", {
      params: { Id: Id },
    });
  }

  // structure nodes

  createNode(data: IOrgStructureNodeRequest) {
    return this.post<IResponse<IOrgStructureNodeModel>>(
      "OrgStructure/Create_Nodes",
      data
    );
  }

  // updateNode(data: IOrgStructureNodeRequest) {
  //   return this.post<IResponse<IOrgStructureNodeModel>>(
  //     "OrgStructure/Edit_Nodes",
  //     data
  //   );
  // }

  DeleteNode(params: IOrgStructureNodeDeleteParams) {
    return this.delete<IResponse>("OrgStructure/Delete_Nodes", {
      params: params,
    });
  }

  // current companies by business id
  getCurrentCompaniesByBusinessId(BusinessId: number) {
    return this.get<IResponse<IOrganizationModel[]>>(
      "OrgStructure/GetCurrentStructureCompnies",
      {
        params: {
          Business_Entity_ID: BusinessId,
        },
      }
    );
  }
}
