import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IReligionRequest, IReligionModel } from "../models/religion";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class ReligionService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IReligionRequest) {
    return this.post<IResponse<IReligionModel>>(
      "HRSetup/Religion/Create",
      data,

    );
  }

  update(data: IReligionRequest) {
    return this.post<IResponse<IReligionModel>>("HRSetup/Religion/Edit", data
   );
  }

  getList(params:IPageParams) {
    return this.get<IResponseList<IReligionModel[]>>(
      "HRSetup/religion/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IReligionModel[]>>("HRSetup/religion/GetAll",
   );
  }

  Delete(id:number) {
    return this.delete<IResponse>("HRSetup/Religion/Delete", {
      params: { Id: id },
    });
  }
}
