import { Injectable } from "@angular/core";
import {
  IAssignJobToOrgDeleteRequest,
  IAssignJobToOrgModel,
  IAssignJobToOrgRequest,
  IJobTitleModel,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class AssignJobToOrgService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IAssignJobToOrgRequest) {
    return this.post<IResponse<IAssignJobToOrgModel>>(
      "OrgManagement/jobtitle/Create_JobTitleAssignOrg",
      data
    );
  }

  getAllByOrgId(orgId: number) {
    return this.get<IResponse<IAssignJobToOrgModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleAssignOrg",
      {
        params: {
          $filter: `org_ID eq ${orgId}`,
        },
      }
    );
  }

  Delete(params: IAssignJobToOrgDeleteRequest) {
    return this.delete<IResponse>(
      "OrgManagement/jobtitle/Delete_JobTitleAssignOrg",
      {
        params: params,
      }
    );
  }

  getAllJobTitleNotAssignedInOrgID(orgId: number) {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleNotInOrgID",
      {
        params: {
          OrgID: orgId,
        },
      }
    );
  }
}
