import { Component, OnInit, Input } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  IJobKpiModel,
  IJobKpiRequest,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { KpiService } from "app/hr-setup/services/kpi.service";
import { IKpiModel } from "app/hr-setup/models/kpi";
import { JobKpiService } from "app/org-management/services/job-profile/job-kpi.service";

@Component({
  selector: "app-job-kpi",
  templateUrl: "./job-kpi.component.html",
  styleUrls: ["./job-kpi.component.scss"],
})
export class JobKpiComponent extends AppHelpers implements OnInit {
  @Input() jobId: number;

  form: FormGroup;
  submitted: boolean = false;

  kpiList: IKpiModel[] = [];

  constructor(
    private jobKpiService: JobKpiService,
    private kpiService: KpiService
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      KPI_Ids: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAllKpis();
      if (this.jobId > 0) this.getAll();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.showLoading();
    this.jobKpiService
      .getAllByJobTitleId(this.jobId)
      .subscribe((res: IResponse<IJobKpiModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.formCtrls.KPI_Ids.setValue(res.data.map((kpi) => kpi.kpI_Id));
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobKpiRequest = {
        JobId: this.jobId,
        KPI_Ids: this.form.value.KPI_Ids,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IJobKpiRequest) {
    this.showLoading();
    this.jobKpiService
      .create(model)
      .subscribe((res: IResponse<IJobKpiModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.getAll();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  // kpi list
  getAllKpis() {
    this.showLoading();
    this.kpiService.getAll().subscribe((res) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        this.kpiList = res.data;
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }
}
