import { Component, Input, OnInit } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import { JobFamiliesComponent } from "app/org-management-setup/components/job-families/job-families.component";
import { IJobFamilyModel } from "app/org-management-setup/models/job-families";
import { JobFamiliesService } from "app/org-management-setup/services/job-families.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-job-family-by-org-dropdown",
	templateUrl: "./job-family-by-org-dropdown.component.html",
	styleUrls: ["./job-family-by-org-dropdown.component.scss"],
})
export class JobFamilyDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() organizationControl: FormGroup;
	@Input() organizationControlRequired: boolean = true;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "jobFamily";
	viewName: string;

	jobFamiliesList: IJobFamilyModel[] = [];
	filteredJobFamiliesList: IJobFamilyModel[] = [];

	constructor(private jobFamiliesService: JobFamiliesService) {
		super();
	}
	override onModulesChanged() {
		this.viewName = ViewsManager.job_families;
	}

	ngOnInit(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllJobFamillies();

		if (this.organizationControlRequired) this.control.disable();
		this.organizationControl?.valueChanges.subscribe((organizationId) => {
			// if org control is required
			if (this.organizationControlRequired) {
				this.resetControl();
				if (organizationId > 0) {
					this.getAllJobFamilliesByOrgId();
					this.control.enable();
				} else this.control.disable();
			}
			// if org control not required
			else {
				if (organizationId > 0) {
					this.resetControl();
					this.getAllJobFamilliesByOrgId();
				}
			}
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany())
			this.organizationControl?.value > 0
				? this.getAllJobFamilliesByOrgId()
				: this.getAllJobFamillies();
	}

	getAllJobFamilliesByOrgId() {
		this.showLoading();
		this.jobFamiliesService
			.getAllByOrgId(this.organizationControl.value)
			.subscribe((res: IResponse<IJobFamilyModel[]>) => {
				if (res.data) {
					this.filteredJobFamiliesList = res.data;
				} else this.errorList(res?.message?.messages);
				this.hideLoading();
			});
	}

	getAllJobFamillies() {
		this.showLoading();
		this.jobFamiliesService.getAll().subscribe((res: IResponse<IJobFamilyModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.jobFamiliesList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	resetControl() {
		setTimeout(() => {
			this.control.reset();
		}, 10);
	}
	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(JobFamiliesComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllJobFamillies();
			});
	}
}
