import { Component, EventEmitter, Input, Output } from "@angular/core";
import { IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-global-functions",
	templateUrl: "./global-functions.component.html",
	styleUrls: ["./global-functions.component.scss"],
})
export class GlobalFunctionsComponent {
	@Input() functions: IRowActionsModel[];
	@Output() action = new EventEmitter<any>();
	appSettings: AppHelpers = new AppHelpers();
	parentView: string;
	params: {};
	constructor() {}

	ngOnInit() {
		this.parentView = this.appSettings.getViewName;
		this.params = {
			parentView: this.parentView,
			...this.appSettings.parantModuleIdParam,
		};
	}

	Action(globalFun: IRowActionsModel) {
		this.action.emit(globalFun);
	}
}
