import { Component, OnInit, Input } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
	IJobCompetenceRequest,
	IJobCompetenceModel,
	IJobCompetenceDeleteRequest,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";
import { MatTableDataSource } from "@angular/material/table";
import { JobCompetencesService } from "app/org-management/services/job-profile/job-competences.service";
import { ICompetenceTypeModel } from "app/org-management-setup/models/competence-type";
import { ICompetenceLevelModel } from "app/org-management-setup/models/competence-level";
import { CompetenceTypeService } from "app/org-management-setup/services/competence-type.service";
import { ICompetenceModel } from "app/org-management/model/competence";
import { CompetenceService } from "app/org-management/services/competence.service";
import { CompetenceLevelService } from "app/org-management-setup/services/competence-level.service";
import { MatDialog } from "@angular/material/dialog";
import { JobCompetenceLevelDetailsDialogComponent } from "../job-competence-level-details-dialog/job-competence-level-details-dialog.component";

@Component({
	selector: "app-job-competences",
	templateUrl: "./job-competences.component.html",
	styleUrls: ["./job-competences.component.scss"],
})
export class JobCompetencesComponent extends AppHelpers implements OnInit {
	@Input() jobId: number;

	form: FormGroup;
	submitted: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"competence",
		"competenceLevel",
		"competencE_Weight",
		"competenceType",
		"actions",
	];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	dataSource: MatTableDataSource<IJobCompetenceModel>;

	competenceList: ICompetenceModel[];
	competenceLevelList: ICompetenceLevelModel[];
	competenceTypeList: ICompetenceTypeModel[];

	constructor(
		private jobCompetencesService: JobCompetencesService,
		private competenceService: CompetenceService,
		private competenceLevelService: CompetenceLevelService,
		private competenceTypeService: CompetenceTypeService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit() {
		this.form = this.fb.group({
			// id: [0, { nonNullable: true }],
			competenceId: [null, [Validators.required]],
			competenceLevelId: [{ value: null, disabled: true }, [Validators.required]],
			competencE_Weight: [
				null,
				[
					Validators.required,
					CustomValidators.digits,
					CustomValidators.gte(0),
					CustomValidators.lte(2147483647),
				],
			],
			competenceTypeId: [null, [Validators.required]],
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllCompetences();
			this.getAllCompetenceTypes();
			if (this.jobId > 0) this.getAll();
		}
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
		this.jobCompetencesService
			.getAllByJobTitleId(this.jobId)
			.subscribe((res: IResponse<IJobCompetenceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((item: IJobCompetenceModel) => {
						item.competence = this.msgTranslate(
							item.core_Competence_Detail?.core_Competence?.ar_Name,
							item.core_Competence_Detail?.core_Competence?.en_Name
						);
						item.competenceLevel = this.msgTranslate(
							item.core_Competence_Detail?.core_Competence_Level?.ar_Name,
							item.core_Competence_Detail?.core_Competence_Level?.en_Name
						);
						item.competenceType = this.msgTranslate(
							item.core_Competence_TYPE?.ar_NAme,
							item.core_Competence_TYPE?.en_Name
						);
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IJobCompetenceRequest = {
				ComptenceID: this.form.value.competenceId,
				ComptenceLevelID: this.form.value.competenceLevelId,
				Comptence_Jobtitle: {
					JOB_Title_ID: this.jobId,
					COMPETENCE_TYPE_ID: this.form.value.competenceTypeId,
					COMPETENCE_Weight: this.form.value.competencE_Weight,
				},
			};

			this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IJobCompetenceRequest) {
		this.showLoading();
		this.jobCompetencesService.create(model).subscribe((res: IResponse<IJobCompetenceModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	onDelete(jobCompetence: IJobCompetenceModel): void {
		let msg = this.msgTranslate(jobCompetence.competence);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(jobCompetence);
		});
	}

	delete(jobCompetence: IJobCompetenceModel) {
		let params: IJobCompetenceDeleteRequest = {
			JobId: this.jobId,
			ComDetailsId: jobCompetence.competencE_Detail_ID,
		};

		this.showLoading();
		this.jobCompetencesService.Delete(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	// Competence List
	getAllCompetences() {
		this.showLoading();
		this.competenceService.getAll().subscribe((res) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.competenceList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	// Competence Level List
	getAllCompetenceLevelsByCompetenceId() {
		// this.competenceLevelList = [];
		let competenceId: number = this.formCtrls.competenceId.value;
		this.formCtrls.competenceLevelId.setValue(null);
		if (competenceId > 0) {
			this.showLoading();
			this.competenceLevelService.getAllByCompetenceId(competenceId).subscribe((res) => {
				if ((res.message.messageType = MessageTypes.Success)) {
					this.competenceLevelList = res.data;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
			this.formCtrls.competenceLevelId.enable();
		} else this.formCtrls.competenceLevelId.disable();
	}

	openCompetenceLevelDetailsDialog() {
		this.dialog
			.open(JobCompetenceLevelDetailsDialogComponent, {
				data: {
					competenceId: this.formCtrls.competenceId.value,
					competencelevelId: this.formCtrls.competenceLevelId.value,
				},
				position: {
					top: "40px",
				},
				width: "85vw",
				panelClass: "dialog-no-padding",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe(() => {});
	}

	// Competence Type List
	getAllCompetenceTypes() {
		this.showLoading();
		this.competenceTypeService.getAll().subscribe((res) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.competenceTypeList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
