import { Injectable } from "@angular/core";
import {
  IJobTrainingPathDeleteRequest,
  IJobTrainingPathModel,
  IJobTrainingPathPriorityModel,
  IJobTrainingPathRequest,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class JobTrainingPathService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobTrainingPathRequest) {
    return this.post<IResponse<IJobTrainingPathModel>>(
      "OrgManagement/jobtitle/Create_JobTitleTrainingPath",
      data
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobTrainingPathModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleTrainingPath",
      {
        params: {
          $filter: `joB_Title_ID eq ${jobId}`,
          $expand: `${db.trA_Course},${db.comm_Priority}`,
        },
      }
    );
  }

  Delete(params: IJobTrainingPathDeleteRequest) {
    return this.delete<IResponse>(
      "OrgManagement/jobtitle/Delete_JobTitleTrainingPath",
      {
        params: params,
      }
    );
  }

  getAllPriority() {
    return this.get<IResponse<IJobTrainingPathPriorityModel[]>>(
      "OrgManagement/JobTitle/GetAll_Priority"
    );
  }
}
