import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ICompanyModel } from "app/auth/models/business-company";
import { AuthService } from "app/auth/services/auth.service";
import { Constants } from "app/shared/resources/constants";
import { Storage } from "app/shared/resources/local-storage";
import { ParamsService } from "app/shared/services/params.service";

@Component({
	selector: "company-by-businessId-dropdown-current-user",
	templateUrl: "./company-dropdown-current-user.component.html",
	styleUrls: ["./company-dropdown-current-user.component.scss"],
})
export class CompanyDropdownCurrentUserComponent implements OnInit {
	@Input() control: FormControl; ///company control
	@Input() businessCtrl?: FormControl; // optional can filter by businessCtrl or general businessId in local storage
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() appendTo: string;
	@Input() label: string = "Company";
	appearance = Constants.defaultInputAppearance;
	//	businessList: IBusinessModel[];
	companyList: ICompanyModel[];
	// businessList: IBusinessModel[] = [];
	// curBusinessId: number;
	// appearance = AppSettings.defaultInputAppearance;
	// currentCompanyId: number;

	constructor(private authService: AuthService, private paramsService: ParamsService) {
		// this.curBusinessId = Storage.getBusinessID;
		// this.currentCompanyId = Storage.getCompanyID;
		// this.authService.BusinessList$.subscribe((business: IBusinessModel[]) => {
		// 	// console.log("BusinessList$", business);
		// 	this.businessList = business;
		// 	this.getCompanies();
		// });
		// this.authService.BusinessID$.subscribe((businessId) => {
		// 	if (this.curBusinessId != businessId) {
		// 		this.curBusinessId = businessId;
		// 		// this.onSelect(null);
		// 		this.getCompanies();
		// 	}
		// });
		// this.authService.CompanyID$.subscribe((companyId) => {
		// 	this.currentCompanyId = companyId;
		// });
	}

	ngOnInit() {}
	ngOnChanges() {
		if (this.businessCtrl) {
			//  filter by businessCtrl
			this.businessCtrl.valueChanges.subscribe((businessId) => {
				if (businessId > 0) {
					this.getCompanies(businessId);
				} else this.companyList = [];
			});
		} else {
			// filter by general businessId in local storage
			this.getCompanies(Storage.getBusinessID);
			this.paramsService.BusinessID$.subscribe((businessId) => {
				this.getCompanies(businessId);
			});
		}
	}

	getCompanies(businessId: number) {
		this.authService.BusinessList$.subscribe((res) => {
			let index = res?.findIndex((e) => e.businessId == businessId);
			this.companyList = res[index]?.companies ?? [];
		});
	}
}
