import { Routes } from "@angular/router";
import { CheckTokenGuard } from "../Guards/check-token.guard";
import { GetModuleIdGuard } from "../Guards/get-module-id.guard";
import { HomeMenuComponent } from "../home-menu/home-menu.component";

//Route for content layout with sidebar, navbar and footer.

export const Full_ROUTES: Routes = [
	{
		path: "home",
		component: HomeMenuComponent,
		canActivate: [CheckTokenGuard],
		data: {
			breadcrumb: {
				label: "home",
			},
		},
	},

	{
		path: "org-management-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../org-management-setup/org-management-setup.module").then(
				(m) => m.OrgManagementSetupModule
			),
	},
	{
		path: "hr-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () => import("../../hr-setup/hr-setup.module").then((m) => m.HrSetupModule),
	},
	{
		path: "personnel-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../personnel-setup/personnel-setup.module").then((m) => m.PersonnelSetupModule),
	},
	{
		path: "payroll-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../payroll-setup/payroll-setup.module").then((m) => m.PayrollSetupModule),
	},
	{
		path: "attendance-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../attendance-setup/attendance-setup.module").then((m) => m.AttendanceSetupModule),
	},
	{
		path: "sap-business-one-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../sap-business-one-setup/sap-business-one-setup.module").then(
				(m) => m.SapBusinessOneSetupModule
			),
	},
	{
		path: "s4-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../s4-setup/s4-setup.module").then((m) => m.S4BusinessOneSetupModule),
	},
	{
		path: "penalty-regulations-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../penalty-regulations-setup/penalty-regulations-setup.module").then(
				(m) => m.PenaltyRegulationsSetupModule
			),
	},
	{
		path: "general-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../general-setup/general-setup.module").then((m) => m.GeneralSetupModule),
		data: {
			breadcrumb: {
				label: "general-setup",
			},
		},
	},
	{
		path: "PMS-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () => import("../../pms-setup/pms-setup.module").then((m) => m.PMSSetupModule),
	},

	{
		path: "org-management/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../org-management/org-management.module").then((m) => m.OrgManagementModule),
	},
	{
		path: "training-setup/:moduleId",
		canActivate: [CheckTokenGuard, GetModuleIdGuard],
		loadChildren: () =>
			import("../../training-setup/training-setup.module").then((m) => m.TrainingSetupModule),
	},
];
