	// 	return this.get(`usermanagement/return-children-functions-by-parentId/${view}`);
	// }

	get isTokenExpired() {
		return this.jwtHelper.isTokenExpired(Storage.getToken);
	}

	get getUserId() {
		// not used now => used from localstorage
		return this.jwtHelper.decodeToken(Storage.getToken).UID;
	}

