import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";
import { db } from "app/shared/resources/database-tabels";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IJobProfileRequest, IJobTitleModel } from "../../model/job-title";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class JobTitleService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobProfileRequest) {
    return this.post<IResponse<IJobTitleModel>>(
      "OrgManagement/JobTitle/Create",
      data
    );
  }

  update(data: IJobProfileRequest) {
    return this.post<IResponse<IJobTitleModel>>(
      "OrgManagement/JobTitle/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetList",
      {
        params: {
          ...params,
          $expand: `${db.Core_Job_Title_Family}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll"
    );
  }

  getAllParentJobTitles(jobId: number) {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll_ParentJobtitle",
      {
        params: {
          Jobid: jobId,
        },
      }
    );
  }

  getById(Id: number) {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll",
      {
        params: {
          $filter: `id eq ${Id}`,
        },
      }
    );
  }

  getAllByOrgId(organizationId: number) {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll",
      {
        params: {
          $expand: db.Core_Org_Job_Title,
        },
      }
    ).pipe(
      map((res: IResponse<IJobTitleModel[]>) => {
        res.data = res.data.filter(
          (job) => job.core_Org_Job_Title[0]?.org_ID == organizationId
        );
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("OrgManagement/JobTitle/Delete", {
      params: { Id: Id },
    });
  }
}
