import { Component, OnInit, Input } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  IJobCustodiesModel,
  IJobCustodiesRequest,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { JobCustodiesService } from "app/org-management/services/job-profile/job-custodies.service";

@Component({
  selector: "app-job-custodies",
  templateUrl: "./job-custodies.component.html",
  styleUrls: ["./job-custodies.component.scss"],
})
export class JobCustodiesComponent extends AppHelpers implements OnInit {
  @Input() jobId: number;

  form: FormGroup;
  submitted: boolean = false;

  constructor(private jobCustodiesService: JobCustodiesService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      custody_Ids: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      if (this.jobId > 0) this.getAll();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.showLoading();
    this.jobCustodiesService
      .getAllByJobTitleId(this.jobId)
      .subscribe((res: IResponse<IJobCustodiesModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.formCtrls.custody_Ids.setValue(
            res.data.map((custody) => custody.covenantId)
          );
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobCustodiesRequest = {
        JobId: this.jobId,
        conv_Ids: this.form.value.custody_Ids,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IJobCustodiesRequest) {
    this.showLoading();
    this.jobCustodiesService
      .create(model)
      .subscribe((res: IResponse<IJobCustodiesModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.getAll();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }
}
