import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { LanguageService } from "app/shared/services/language.service";
import { MultiSelect } from "primeng/multiselect";

@Component({
  selector: "app-prime-dropdown",
  templateUrl: "./dropdown.component.html",
  styleUrls: ["./dropdown.component.scss"],
})
export class DropdownComponent extends AppHelpers implements OnInit {
  @Input() control: FormControl;
  @Input() label: string;
  @Input() List: Idropdown[] = null;
  @Input() multi: boolean = false;
  @Input() submitted: boolean = false;
  @Input() filter: boolean = false;
  @Input() readonly: boolean = false;
  @Input() optionDisabled: string = null;
  @Input() class: string;
  @Input() appendTo: string = "body";
  @Input() viewName: string;
  @Output() change = new EventEmitter<any>();
  @Output() createNewDialog = new EventEmitter<any>();
  @Input() alloweCreateNew: boolean = false;
  @ViewChild("multiSelectRef") multiSelectRef!: MultiSelect;

  filterValue: string;
  isLoaded: boolean = false;
  constructor(public languageService: LanguageService) {
    super();
  }

  ngOnInit() {
    this.control?.valueChanges.subscribe((id) => {
      this.onChange(id);
    });

    this.alloweCreateNew = this.isViewAllowed(this.viewName);
  }

  ngOnChanges(changes: SimpleChanges) {
    //	console.log("List", this.List);
    if (this.List?.length >= 0) this.isLoaded = true;
    if (
      changes.List?.previousValue?.length == 0 &&
      changes.List?.currentValue?.length != 0
    ) {
      setTimeout(() => {
        this.control.markAsPristine();
      }, 100);
      // if list get its value after control
      if (this.control.value > 0) this.onChange(this.control.value);
    }
  }

  override onModulesChanged() {
    this.alloweCreateNew = this.isViewAllowed(this.viewName);
  }

  onChange(value: any) {
    let selectedObject: any[];
    if (value > 0) {
      selectedObject = this.multi
        ? this.List.filter((ele) => value.includes(ele.id))
        : (selectedObject = this.List.filter((ele) => ele.id == value));
    }
    // console.log(selectedObject)
    this.change.emit(selectedObject);
  }

  onCreateDialog(value: string) {
    this.createNewDialog.emit(value);
    this.multiSelectRef.hide();// to hide create new button when dialog is opened
  }

  onFilter($event) {
    this.filterValue = $event.filter;
  }
}
