import { Injectable } from "@angular/core";
import { ICountryRequest, ICountryModel } from "app/hr-setup/models/country";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
	providedIn: "root",
})
export class CountryService extends AppClientService {
	constructor() {
		super();
	}

	create(data: ICountryRequest) {
		return this.post<IResponse<ICountryModel>>("HRSetup/country/Create", data);
	}

	update(data: ICountryRequest) {
		return this.post<IResponse<ICountryModel>>("HRSetup/country/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<ICountryModel[]>>("HRSetup/Country/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<ICountryModel[]>>("HRSetup/Country/GetAll").pipe(
			map((res: IResponse<ICountryModel[]>) => {
				res.data.map((item: ICountryModel) => {
					item.en_Name = item.en_NAme;
				});
				return res;
			})
		);
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/Country/Delete", {
			params: { Id: id },
		});
	}
}
