import { Injectable } from "@angular/core";
import {
  ISystemInstancePurposeModel,
  ISystemInstancePurposeRequest,
} from "app/sap-business-one-setup/models/system-instance";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class SystemInstancePurposeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISystemInstancePurposeRequest) {
    return this.post<IResponse<ISystemInstancePurposeModel>>(
      "SystemInstanceS1Setup/Create_Purpose_SB1",
      data
    );
  }

  update(data: ISystemInstancePurposeRequest) {
    return this.post<IResponse<ISystemInstancePurposeModel>>(
      "SystemInstanceS1Setup/Edit_Purpose_SB1",
      data
    );
  }

  getAllBySystemInstanceId(systemInstanceId: number) {
    return this.get<IResponseList<ISystemInstancePurposeModel[]>>(
      "SystemInstanceS1Setup/GetAll_Purpose_SB1",
      {
        params: {
          $filter: `SystemInstance_ID eq ${systemInstanceId}`,
        },
      }
    ).pipe(
      map((res) => {
        res.data.map((item: ISystemInstancePurposeModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SystemInstanceS1Setup/Delete_Purpose_SB1", {
      params: { Id: Id },
    });
  }
}
