import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { CustodiesComponent } from "app/org-management-setup/components/custodies/custodies.component";
import { ICustodyModel } from "app/org-management-setup/models/custodies";
import { CustodiesService } from "app/org-management-setup/services/custodies.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-custodies-dropdown",
  templateUrl: "./custodies-dropdown.component.html",
  styleUrls: ["./custodies-dropdown.component.scss"],
})
export class CustodiesDropdownComponent extends AppHelpers implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "custodies";
  viewName: string;
  @Input() appendTo: string = "body";
  @Output() change = new EventEmitter<ICustodyModel[]>();

  custodies: ICustodyModel[]  ;

  constructor(private custodiesService: CustodiesService) {
    super();
    this.viewName = ViewsManager.custodies;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllCustodies();
  }

  getAllCustodies() {
    this.showLoading();
    this.custodiesService
      .getAll()
      .subscribe((res: IResponse<ICustodyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.custodies = res.data;
        } else this.errorList(res.message?.messages);
        this.hideLoading();
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(CustodiesComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllCustodies();
      });
  }

  onChange(countries: ICustodyModel[]) {
    this.change.emit(countries);
  }
}
