import { Injectable } from '@angular/core';
import {IDeleteCompaniesRequest, IUserCompaniesModel, IUserCompaniesRequest } from 'app/general-setup/models/users';
import { IResponse } from 'app/shared/interfaces/responces';
import { AppClientService } from 'app/shared/services/app-client.service';
import { db } from 'app/shared/resources/database-tabels';

@Injectable({
  providedIn: 'root'
})
export class UserCompanyService  extends AppClientService {
	constructor() {
		super();
	}

  //----------------- User Companies Services -------------------

	createUserCompany(data: IUserCompaniesRequest) {
		return this.post<IResponse<IUserCompaniesModel>>("GeneralSetup/User/SaveUser_Company", data

		);
	}

	updateUserCompany(data: IUserCompaniesRequest) {
		return this.post<IResponse<IUserCompaniesModel>>("GeneralSetup/User/EditUser_Company", data

		);
	}

	getUserCompanyList( id: number) {
		return this.get<IResponse<IUserCompaniesModel[]>>("GeneralSetup/User/GetUserCompanies", {
			params: {
					$expand: `${db.core_Business_Entity},${db.company}`,
				$filter: "User_ID eq " + id,
			},
		});
	}


	DeleteUserCompany(body: IDeleteCompaniesRequest) {
		return this.delete<IResponse>("GeneralSetup/User/DeleteUser_Company", { body });
	}
}
