import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IBusinessEntityModel } from "app/org-management/model/business-entity";
import { BusinessEntityService } from "app/org-management/services/business-entity.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-Business-dropdown",
  templateUrl: "./Business-dropdown.component.html",
  styleUrls: ["./Business-dropdown.component.scss"],
})
export class BusinessDropdownComponent extends AppHelpers implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "Business";

  businessList: IBusinessEntityModel[];

  constructor(private businessEntityService: BusinessEntityService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllBusiness();
  }

  getAllBusiness() {
    this.showLoading();
    this.businessEntityService
      .getAll()
      .subscribe((res: IResponse<IBusinessEntityModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.businessList = res.data;
        } else this.errorList(res.message?.messages);
        this.hideLoading();
      });
  }
}
