import { Injectable } from "@angular/core";
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
} from "@angular/router";
import { Storage } from "app/shared/resources/local-storage";

@Injectable({
  providedIn: "root",
})
export class GetModuleIdGuard implements CanActivate {
  constructor() {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): boolean {
    let parantModuleId = route.queryParams.parantModuleId;
    let moduleId = route.params.moduleId;
    Storage.setParentModule(parantModuleId);
    Storage.setSelectedModule(moduleId);

    return true;
  }
}
