import {
	ChangeDetectorRef,
	Component,
	EventEmitter,
	Input,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild,
} from "@angular/core";
import { MatPaginator, PageEvent } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { AssetsManager } from "app/shared/resources/assets-manager";
import { IGUIModel, IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers, Page } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { FileService } from "app/shared/services/file.service";
import * as FileSaver from "file-saver";

export interface megingImgWithColumnData {
  isMerge: boolean;
  columnImgName: string;
  columnTextName: string;
}

@Component({
	selector: "app-material-table",
	templateUrl: "./material-table.component.html",
	styleUrls: ["./material-table.component.scss"],
})
export class MaterialTableComponent implements OnInit {
	@Input() dataSource: MatTableDataSource<any> | any;
	@Input() mandatoryColomns: string[] = [];
	@Input() optionalColomns: string[] = [];
	@Input() page: Page;
	@Input() actions: IRowActionsModel;
	@Input() isHexImg: boolean = false;
	@Input() showPaginator: boolean = true;
	@Input() showFilter: boolean = true;
	@Input() showLoader: boolean = true;
	@Input() isLoaded: boolean = false;
	@Input() mergeImgWithName: megingImgWithColumnData = null;
	@Output() onChangePage = new EventEmitter<PageEvent>();
	@Output() deleteRow = new EventEmitter<any>();
	@Output() editRow = new EventEmitter<any>();
	@Output() rowactions = new EventEmitter<any>();

	@ViewChild(MatSort) sort: MatSort;
	@ViewChild(MatPaginator) paginator: MatPaginator;

	appSettings: AppHelpers = new AppHelpers();
	parentView: string;
	params;
	displayedColumns: string[] = [];
	PageSizeOptions = Constants.pageSizeOptions;
	assetsManager: AssetsManager = new AssetsManager();
	constructor(public fileService: FileService, private changeDetectorRef: ChangeDetectorRef) {}

	ngOnInit(): void {
		this.displayedColumns = this.mandatoryColomns.slice();
		this.parentView = this.appSettings.getViewName;
		this.params = {
			parentView: this.parentView,
			...this.appSettings.pageParams,
		};
	}

	ngOnChanges(changes: SimpleChanges): void {
		if (!changes.dataSource?.firstChange && changes.dataSource?.currentValue.data)
			this.isLoaded = true;
	}

	ngAfterViewChecked() {
		if (this.sort) this.dataSource.sort = this.sort;
	}

	// page change event
	change(page: PageEvent) {
		this.params.page = page.pageIndex + 1;
		this.onChangePage.emit(page);
		this.dataSource = null;
		this.isLoaded = false;
	}

	// globale action event
	Action(data: any, fun: IGUIModel) {
		if (fun.name.toLocaleLowerCase().search("edit") != -1) this.edit(data, fun);
		if (fun.name.toLocaleLowerCase().search("delete") != -1) this.delete(data);
		else this.rowactions.emit({ data, fun: fun });
	}

	// edit row event
	edit(data, fun: IGUIModel) {
		//let row = {} as any;
		data.function = fun;
		this.editRow.emit(data);
	}

	// delete row event
	delete(data) {
		this.deleteRow.emit(data);
	}

	applyFilter(filterValue: string) {
		this.dataSource.filter = filterValue.trim().toLowerCase();
	}

	editColumnsToView(index: number, selected: boolean) {
		if (selected)
			this.displayedColumns.splice(
				this.displayedColumns.length - 1,
				0,
				this.optionalColomns[index]
			);
		else {
			let indexInTable = this.displayedColumns.findIndex(
				(ele) => ele == this.optionalColomns[index]
			);
			this.displayedColumns.splice(indexInTable, 1);
		}
	}

	isImage(col: string, value: any): boolean {
    return col == "logo" ||
    (typeof value === "string" && value.includes("Attachments/")) ||
    // display image with en name
    (this.mergeImgWithName?.isMerge &&
      col == this.mergeImgWithName?.columnTextName)
    ? true
    : false;
	}

	isBooleanType(value: any): boolean {
		return typeof value === "boolean";
	}

	exportExcel() {
		import("xlsx").then((xlsx) => {
			const worksheet = xlsx.utils.json_to_sheet(this.dataSource.data ?? this.dataSource);
			const workbook = { Sheets: { data: worksheet }, SheetNames: ["data"] };
			const excelBuffer: any = xlsx.write(workbook, { bookType: "xlsx", type: "array" });
			this.saveAsExcelFile(excelBuffer, this.appSettings.title.getTitle());
		});
	}

	saveAsExcelFile(buffer: any, fileName: string): void {
		let EXCEL_TYPE =
			"application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
		let EXCEL_EXTENSION = ".xlsx";
		const data: Blob = new Blob([buffer], {
			type: EXCEL_TYPE,
		});
		FileSaver.saveAs(data, fileName + "_export_" + new Date().getDate() + EXCEL_EXTENSION);
	}
}
