import {
	AfterViewInit,
	ChangeDetectorRef,
	Component,
	ElementRef,
	HostListener,
	OnDestroy,
	OnInit,
	ViewChild,
	inject,
} from "@angular/core";

import { AuthService } from "app/auth/services/auth.service";
import { UserViewsService } from "app/auth/services/user-views.service";
import { DeviceDetectorService } from "ngx-device-detector";
import { Subscription } from "rxjs";
import { AssetsManager } from "../resources/assets-manager";
import { AppHelpers } from "../resources/app-helpers";
import { LanguageService } from "../services/language.service";
import { Storage } from "../resources/local-storage";
import { ParamsService } from "../services/params.service";
import { ConfigService } from "../services/theme-options/config.service";
import { LayoutService } from "../services/theme-options/layout.service";

@Component({
	selector: "app-sidebar",
	templateUrl: "./vertical-menu.component.html",
})
export class VerticalMenuComponent implements OnInit, AfterViewInit, OnDestroy {
	@ViewChild("toggleIcon") toggleIcon: ElementRef;
	public menuItems: any[] = [];
	level: number = 0;
	appAssets: AssetsManager = new AssetsManager();

	logoUrl = this.appAssets.logo;
	public config: any = {};
	protected innerWidth: any;
	layoutSub: Subscription;
	configSub: Subscription;
	menuSub: Subscription;
	perfectScrollbarEnable = true;
	collapseSidebar = false;
	resizeTimeout;
	isUserModelCalled = false;
	appSettings = new AppHelpers();
	private paramsService = inject(ParamsService);
	constructor(
		public viewsService: UserViewsService,
		public langService: LanguageService,
		public authService: AuthService,
		private layoutService: LayoutService,
		private configService: ConfigService,
		private cdr: ChangeDetectorRef,
		private deviceService: DeviceDetectorService
	) {
		this.config = this.configService.templateConf;
		this.innerWidth = window.innerWidth;
		this.isTouchDevice();

		this.paramsService.businessCompanyParams$.subscribe((val) => {
			if (
				this.appSettings.checkNoBusinessOrCompany() &&
				(Storage.getParentModule || Storage.getSelectedModule)
			) {
				this.getUserModules();
			}
		});

		this.viewsService.UserModules$.subscribe((val) => {
			this.viewsService.getModuleServices(Storage.getParentModule);
			if (!Storage.getParentModule) this.viewsService.getModuleServices(Storage.getSelectedModule);
		});

		this.viewsService.selectedModuleId$.subscribe((moduleId) => {
			//  console.log("moduleId", moduleId);
			this.viewsService.getModuleServices(moduleId);
		});

		this.menuSub = this.viewsService.menuItems$.subscribe((data) => {
			setTimeout(() => {
				this.menuItems = data;
			}, 0);

			// console.log("menuItems", this.menuItems);
		});
	}

	ngOnInit() {}

	ngAfterViewInit() {
		this.configSub = this.configService.templateConf$.subscribe((templateConf) => {
			if (templateConf) {
				this.config = templateConf;
			}
			this.loadLayout();
			this.cdr.markForCheck();
		});

		this.layoutSub = this.layoutService.overlaySidebarToggle$.subscribe((collapse) => {
			if (this.config.layout.menuPosition === "Side") {
				this.collapseSidebar = collapse;
			}
		});
	}

	getUserModules() {
		if (!this.isUserModelCalled) this.viewsService.getUserModules();
		this.isUserModelCalled = true;
	}

	@HostListener("window:resize", ["$event"])
	onWindowResize(event) {
		if (this.resizeTimeout) {
			clearTimeout(this.resizeTimeout);
		}
		this.resizeTimeout = setTimeout(
			(() => {
				this.innerWidth = event.target.innerWidth;
				this.loadLayout();
			}).bind(this),
			500
		);
	}

	loadLayout() {
		if (this.config.layout.menuPosition === "Top") {
			// Horizontal Menu
			if (this.innerWidth < 1200) {
				// Screen size < 1200
				//    this.menuItems = HROUTES;
			}
		} else if (this.config.layout.menuPosition === "Side") {
			// Vertical Menu{
			//  this.menuItems = ROUTES;
		}

		if (this.config.layout.sidebar.backgroundColor === "white") {
			this.logoUrl = this.appAssets.darkLogo;
		} else {
			this.logoUrl = this.appAssets.logo;
		}

		if (this.config.layout.sidebar.collapsed) {
			this.collapseSidebar = true;
		} else {
			this.collapseSidebar = false;
		}
	}

	toggleSidebar() {
		let conf = this.config;
		conf.layout.sidebar.collapsed = !this.config.layout.sidebar.collapsed;
		this.configService.applyTemplateConfigChange({ layout: conf.layout });

		setTimeout(() => {
			this.fireRefreshEventOnWindow();
		}, 300);
	}

	fireRefreshEventOnWindow = function () {
		const evt = document.createEvent("HTMLEvents");
		evt.initEvent("resize", true, false);
		window.dispatchEvent(evt);
	};

	CloseSidebar() {
		this.layoutService.toggleSidebarSmallScreen(false);
	}

	isTouchDevice() {
		const isMobile = this.deviceService.isMobile();
		const isTablet = this.deviceService.isTablet();

		if (isMobile || isTablet) {
			this.perfectScrollbarEnable = false;
		} else {
			this.perfectScrollbarEnable = true;
		}
	}

	ngOnDestroy() {
		Storage.removeSelectedModule();
		this.menuSub.unsubscribe();
		this.menuItems = [];
		this.viewsService.menuItems$.next([]);

		if (this.layoutSub) {
			this.layoutSub.unsubscribe();
		}
		if (this.configSub) {
			this.configSub.unsubscribe();
		}
	}
}
