import { Component, OnInit, inject } from "@angular/core";
import { IUM_ModulesModel } from "../interfaces/user-views";
import { AppHelpers } from "../resources/app-helpers";
import { Storage } from "../resources/local-storage";
import { ParamsService } from "../services/params.service";

@Component({
	selector: "app-home-menu",
	templateUrl: "./home-menu.component.html",
	styleUrls: ["./home-menu.component.scss"],
})
export class HomeMenuComponent extends AppHelpers implements OnInit {
	paramsService = inject(ParamsService);
	constructor() {
		super();
		// if (this.businessId > 0 && this.companyId > 0) {
		// 	this.getUserModules();
		// }
	}

	ngOnInit() {
		Storage.setSelectedModule(null);
		Storage.setParentModule(null);
		this.viewsService.selectedModuleId$.next(null);

		this.paramsService.businessCompanyParams$.subscribe((res) => {
			// if (res.BusinessEntityID > 0 && res.CompanyID > 0) {
			// 	this.getUserModules();
			// }
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		//	console.log("home-menu", this.businessCompanyParams);
		this.getUserModules();
	}

	selectModule(mod: IUM_ModulesModel) {
		Storage.setSelectedModule(mod.id);
		this.viewsService.selectedModuleId$.next(mod.id);
	}

	navigateToService(mod: IUM_ModulesModel) {
		let url = null;
		url = mod.children
			? `/${mod.children[0]?.uM_Services[0].path}`
			: `/${mod.uM_Services[0]?.path}`;
		// url = `/${mod.path}/${mod.id}/${mod.uM_Services[1]?.path}`;
		return url;
	}

	getUserModules() {
		this.viewsService.getUserModules();
	}
}
