import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IPayVariable } from "app/payroll/models/variables";
import { VariablesService } from "app/payroll/services/variables.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-variable-by-payment-dropdown",
	templateUrl: "./variable-by-payment-dropdown.component.html",
	styleUrls: ["./variable-by-payment-dropdown.component.scss"],
})
export class VariableByPaymentDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() paymentControl: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "variable";

	variableList: IPayVariable[] = [];

	constructor(private variablesService: VariablesService) {
		super();
	}

	ngOnInit(): void {
		this.control.disable();

		this.paymentControl?.valueChanges.subscribe((paymentId) => {
			this.control.setValue(null);

			if (paymentId > 0) {
				this.getAllVariablesByPayment();
				this.control.enable();
			} else this.control.disable();
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.paymentControl?.value > 0 && this.checkNoBusinessOrCompany())
			this.getAllVariablesByPayment();
	}

	getAllVariablesByPayment() {
		this.showLoading();
		this.variablesService.GetAllByPaymentId(this.paymentControl?.value).subscribe((res) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.variableList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
