import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { ICourseModel } from "../models/course";

@Injectable({
  providedIn: "root",
})
export class CourseService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<ICourseModel[]>>("Training/Course/GetAll");
  }
}
