import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import {
  IS4SystemInstanceModel,
  IS4SystemInstanceRequest,
} from "app/s4-setup/models/s4-system-instance";
import { S4SytemInstanceService } from "app/s4-setup/services/s4-sytem-instance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
@Component({
  selector: "app-add-s4-system-instance",
  templateUrl: "./add-s4-system-instance.component.html",
  styleUrls: ["./add-s4-system-instance.component.scss"],
})
export class AddS4SystemInstanceComponent
  extends AppHelpers
  implements OnInit
{
  form: FormGroup;
  submitted: boolean = false;

  // s4SystemInstance: IS4SystemInstanceModel;
  s4SystemInstanceId: number;

  constructor(
    private s4SytemInstanceService: S4SytemInstanceService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddS4SystemInstanceComponent>
  ) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.s4SystemInstanceId = Number(params.get("s4SystemInstanceId")) ?? 0;
      // if (this.s4SystemInstanceId > 0 && this.CheckNoBusinessOrCompany())
      //   this.getById();
    });

    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      api_Url_Prefix: [null, [Validators.required, CustomValidator.whiteSpace]],
      userName: [null, [Validators.required]],
      password: [null, [Validators.required, CustomValidator.whiteSpace]],
      s4_Client_Code: [null, [Validators.required, CustomValidator.whiteSpace]],
      s4_Company_Code: [
        null,
        [Validators.required, CustomValidator.whiteSpace],
      ],
    });
    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.s4SystemInstance = null;
    if (this.s4SystemInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getById();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.s4SytemInstanceService
      .getById(this.s4SystemInstanceId)
      .subscribe((res: IResponse<IS4SystemInstanceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.s4SystemInstance = res.data[0];
          this.s4SystemInstanceToEdit(res.data[0]);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  s4SystemInstanceToEdit(s4SystemInstance: IS4SystemInstanceModel) {
    this.form.patchValue(s4SystemInstance);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IS4SystemInstanceRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_Name,
        En_Name: this.form.value.en_Name,
        Api_Url_Prefix: this.form.value.api_Url_Prefix,
        UserName: this.form.value.userName,
        Password: this.form.value.password,
        S4_Client_Code: this.form.value.s4_Client_Code,
        S4_Company_Code: this.form.value.s4_Company_Code,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IS4SystemInstanceRequest) {
    this.showLoading();
    this.s4SytemInstanceService
      .create(model)
      .subscribe((res: IResponse<IS4SystemInstanceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          // this.clearForm();
          this.s4SystemInstanceId = res.data.id;
          this.formCtrls.id.setValue(this.s4SystemInstanceId);
          let url = `/s4-setup/${this.moduleId}/s4-system-instance/edit/${this.s4SystemInstanceId}`;
          this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.s4SystemInstance = res.data;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IS4SystemInstanceRequest) {
    this.showLoading();
    this.s4SytemInstanceService
      .update(model)
      .subscribe((res: IResponse<IS4SystemInstanceModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          // this.s4SystemInstance = res.data;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    // if (this.s4SystemInstanceId == 0)
    this.form.reset({ id: 0 });
    // else this.s4SystemInstanceToEdit();
    this.submitted = false;
  }
  onCloseDialog() {
    this.dialogRef.close();
  }
}
