import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { CountryComponent } from "app/hr-setup/components/country/country.component";
import { ICountryModel } from "app/hr-setup/models/country";
import { CountryService } from "app/hr-setup/services/country.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-country-dropdown",
	templateUrl: "./country-dropdown.component.html",
	styleUrls: ["./country-dropdown.component.scss"],
})
export class GovernorateDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "country";
	@Input() appendTo: string;
	@Output() change = new EventEmitter<ICountryModel[]>();

	countrytList: ICountryModel[];
	viewName: string;

	constructor(private countryService: CountryService) {
		super();
		this.viewName = ViewsManager.country;
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllCountry();
	}

	getAllCountry() {
		this.showLoading();
		this.countryService.getAll().subscribe((res: IResponseList<ICountryModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.countrytList = res.data;
			} else this.errorList(res.message?.messages);
			this.hideLoading();
		});
	}

	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(CountryComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllCountry();
			});
	}

	onChange(countries: ICountryModel[]) {
		this.change.emit(countries);
	}
}
