import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { VacationTypeComponent } from "app/attendance-setup/components/vacation/vacation-type/vacation-type.component";
import { IVacationModel, IVacationResponse } from "app/attendance-setup/models/vacation";
import { VacationService } from "app/attendance-setup/services/vacation/vacation.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-vacation-type-dropdown",
	templateUrl: "./vacation-type-dropdown.component.html",
	styleUrls: ["./vacation-type-dropdown.component.scss"],
})
export class VacationTypeDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "vacations";
	@Output() change = new EventEmitter<IVacationModel>();
	viewName: string;
	vacationList: IVacationModel[] = [];

	constructor(private vacationServ: VacationService) {
		super();
		this.viewName = ViewsManager.add_vacation;
	}

	ngOnInit(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllVacations();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllVacations();
	}

	getAllVacations() {
		this.showLoading();
		this.vacationServ.getAll().subscribe((res: IResponse<IVacationResponse[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				res.data.map((att) => {
					this.vacationList.push(att.att_Vacation);
				});
			} else {
				this.errorList(res.message.messages);
				this.hideLoading();
			}
		});
	}

	onChange(att_Vacation: IVacationModel) {
		this.change.emit(att_Vacation);
	}
	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(VacationTypeComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllVacations();
			});
	}
}
