import { Component, Input, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { IBankModel } from "app/hr-setup/models/bank";
import { BankService } from "app/hr-setup/services/bank/bank.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddBankComponent } from "app/hr-setup/components/bank/add-bank/add-bank.component";

@Component({
  selector: "app-bank-dropdown",
  templateUrl: "./bank-dropdown.component.html",
  styleUrls: ["./bank-dropdown.component.scss"],
})
export class BankDropdownComponent extends AppHelpers implements OnInit {
  @Input() control: FormGroup;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "bank";
  @Input() appendTo: string = "body";
  viewName: string;
  bankList: IBankModel[];

  constructor(private bankService: BankService) {
    super();
    this.viewName = ViewsManager.add_bank;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllBanks();
  }

  getAllBanks() {
    this.showLoading();
    this.bankService.getAll().subscribe((res: IResponse<IBankModel[]>) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        this.bankList = res.data;
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddBankComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllBanks();
      });
  }
}
