import { Injectable } from "@angular/core";
import { ICustodyModel, ICustodyRequest } from "app/org-management-setup/models/custodies";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class CustodiesService extends AppClientService {
	constructor() {
		super();
	}

	create(data: ICustodyRequest) {
		return this.post<IResponse<ICustodyModel>>("custodies/Create", data);
	}

	update(data: ICustodyRequest) {
		return this.post<IResponse<ICustodyModel>>("custodies/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<ICustodyModel[]>>("Custodies/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<ICustodyModel[]>>("Custodies/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("custodies/Delete", {
			params: { Id: id },
		});
	}
}
