import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { SystemInstancePurposeComponent } from "app/sap-business-one-setup/components/system-instance/system-instance-purpose/system-instance-purpose.component";
import { ISystemInstancePurposeModel } from "app/sap-business-one-setup/models/system-instance";
import { SystemInstancePurposeService } from "app/sap-business-one-setup/services/system-instance-purpose.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-sap-integration-purpose-by-system-instance-dropdown",
	templateUrl: "./sap-integration-purpose-by-system-instance-dropdown.component.html",
	styleUrls: ["./sap-integration-purpose-by-system-instance-dropdown.component.scss"],
})
export class SapIntegrationPurposeBySystemInstanceDropdownComponent
	extends AppHelpers
	implements OnInit
{
	@Input() control: FormControl;
	@Input() systemInstanceControl: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "integrationPurpose";
	viewName: string;

	integrationPurposeList: ISystemInstancePurposeModel[] = [];

	constructor(private systemInstancePurposeService: SystemInstancePurposeService) {
		super();
		// add parent viewname beacuse it does  not have route
		this.viewName = ViewsManager.add_system_instance;
	}

	ngOnInit(): void {
		this.control.disable();

		this.systemInstanceControl?.valueChanges.subscribe((systemInstanceId) => {
			this.control.setValue(null);

			if (systemInstanceId > 0) {
				this.getAllIntegrationPurposesBySystemInstance();
				this.control.enable();
			} else this.control.disable();
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.systemInstanceControl?.value > 0 && this.checkNoBusinessOrCompany())
			this.getAllIntegrationPurposesBySystemInstance();
	}

	getAllIntegrationPurposesBySystemInstance() {
		this.showLoading();
		this.systemInstancePurposeService
			.getAllBySystemInstanceId(this.systemInstanceControl.value)
			.subscribe((res: IResponseList<ISystemInstancePurposeModel[]>) => {
				if ((res.message.messageType = MessageTypes.Success)) {
					this.integrationPurposeList = res.data;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}
	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(
				SystemInstancePurposeComponent,
				value,
				this.systemInstanceControl.value
			)
			.afterClosed()
			.subscribe((res) => {
				this.getAllIntegrationPurposesBySystemInstance();
			});
	}
}
