import { Component, EventEmitter, Inject, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IBusinessModel, ICompanyModel } from "app/auth/models/business-company";
import { IReligionModel } from "app/hr-setup/models/religion";
import { ReligionService } from "app/hr-setup/services/religion.service";
import { IEmployeeModel, IEmployeeRequest } from "app/personnel-setup/models/employees";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-employee-general-info-dialog",
	templateUrl: "./emp-general-info-dialog.component.html",
	styleUrls: ["./emp-general-info-dialog.component.scss"],
})
export class EmployeeGeneralInfoDialogComponent extends AppHelpers implements OnInit {
	@Output() afterAdd = new EventEmitter<IEmployeeModel>();
	employeeId: number = 0;
	businessAndCompanyList: IBusinessModel[];
	form: FormGroup;
	submitted: boolean = false;
	updated: boolean = false;

	constructor(
		private employeesService: EmployeesService,
		@Inject(MAT_DIALOG_DATA)
		public data: {
			employee: IEmployeeModel;
		},
		@Inject(MAT_DIALOG_DATA)
		public dataFromDropdown: ICreateDialog,
		public dialogRef: MatDialogRef<EmployeeGeneralInfoDialogComponent>
	) {
		super();
		if (data.employee) {
			this.employeeId = data.employee.id;
		}
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			code: [null, [Validators.required, CustomValidator.whiteSpace]],
			CompanyCode: [null],
			external_Code: [null],
			gender_ID: [null],
			nationalid: [
				null,
				[
					Validators.required,
					CustomValidator.whiteSpace,
					Validators.maxLength(14),
					Validators.minLength(14),
					CustomValidators.number,
				],
			],
			religioN_ID: [null],
			dob: [null, [Validators.required, CustomValidators.date]],
			hireDate: [null, [CustomValidators.date]],
			blooD_TYPE_ID: [null],
			insurance_Years: [null, [CustomValidators.number]],
			insurance_No: [null],
			nationality: [null],
			SharedCompanies: [false],
			businessIds: [[this.businessCompanyParams.BusinessEntityID]],
			companyIds: [[this.businessCompanyParams.CompanyID]],
		});

		if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
			this.employeeToEdit(this.data.employee);

		// to change dropdown (company, business) > use dynamic

		this.dialogRef.backdropClick().subscribe((ele) => {
			this.onCloseDialog();
		});

		if (this.dataFromDropdown.value) {
			this.formCtrls.ar_Name.setValue(this.dataFromDropdown.value);
			this.formCtrls.en_Name.setValue(this.dataFromDropdown.value);
		}
	}

	onCloseDialog() {
		this.dialogRef.close(this.updated);
	}

	get formCtrls() {
		return this.form.controls;
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeRequest = {
				person: {
					id: this.form.value.id,
					ar_Name: this.form.value.ar_Name,
					en_Name: this.form.value.en_Name,
					code: this.form.value.code,
					external_Code: this.form.value.external_Code ?? null,
					gender_ID: this.form.value.gender_ID,
					blooD_TYPE_ID: this.form.value.blooD_TYPE_ID,
					dob: HelpersFunctions.mapDateReverse(this.form.value.dob),
					hireDate: HelpersFunctions.mapDateReverse(this.form.value.hireDate),
					insurance_Years: this.form.value.insurance_Years,
					insurance_No: this.form.value.insurance_No,
					nationalid: this.form.value.nationalid,
					religioN_ID: this.form.value.religioN_ID,
				},
				companyCode: this.form.value.CompanyCode,
				nationality: this.form.value.Nationality,
				sharedCompanies: [
					{
						company_Id: this.businessCompanyParams.CompanyID,
						isDefaultCompany: true,
					},
				],
			};
			// if SharedCompanies is selected
			if (this.formCtrls.SharedCompanies.value == true)
				this.formCtrls.companyIds.value.forEach((id: number) => {
					if (id != this.businessCompanyParams.CompanyID)
						model.sharedCompanies.push({
							company_Id: id,
							isDefaultCompany: false,
						});
				});

			model.person.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeRequest) {
		this.showLoading();
		this.employeesService.create(model).subscribe((res: IResponse<IEmployeeRequest>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.employeeId = res.data.person.id;
				// this.setUpdatedData(res);
				// change url && employee id
				this.afterAdd.emit(res.data.person);
				this.updated = true;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IEmployeeRequest) {
		this.showLoading();
		this.employeesService.update(model).subscribe((res: IResponse<IEmployeeRequest>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				// this.setUpdatedData(res);
				this.updated = true;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	// setUpdatedData(res: IResponse<IEmployeeRequest>) {
	// 	this.employeeInfo = {
	// 		...res.data.person,
	// 		nationality: res.data.nationality,
	// 		companyCode: res.data.companyCode,
	// 		sharedCompanies: res.data.sharedCompanies,
	// 	};
	// 	this.employeeToEdit();
	// 	// console.log(this.employeeInfo);
	// }

	employeeToEdit(employeeInfo: IEmployeeModel) {
		this.form.patchValue(employeeInfo);
		// console.log(this.form.value);
	}

	// clearForm() {
	// 	if (this.employeeId == 0)
	// 		this.form.reset({
	// 			id: 0,
	// 			SharedCompanies: false,
	// 			businessIds: [this.businessCompanyParams.BusinessEntityID],
	// 			companyIds: [this.businessCompanyParams.CompanyID],
	// 		});
	// 	else this.employeeToEdit();
	// 	this.submitted = false;
	// 	// console.log(this.form.value);
	// }

	onChangeCompany(values: ICompanyModel[]) {
		let ids = values.map((ele) => ele.id);
		// prevent remove current company id
		if (!ids.includes(this.businessCompanyParams.CompanyID)) {
			ids.unshift(this.businessCompanyParams.CompanyID);
			this.formCtrls.companyIds.setValue(ids);
		}
	}
}
