import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IReportsModel, IsoCodeRequest } from "../models/iso-code";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
	providedIn: "root",
})
export class IsoCodeService extends AppClientService {
	constructor() {
		super();
	}

	Edit(data: IsoCodeRequest) {
		return this.post<IResponse<IReportsModel>>("Report/Report/Edit", data);
	}

	GetAllReports() {
		return this.get<IResponse<IReportsModel[]>>("Report/Report/GetAll");
	}
}
