import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";
import { IPaymentModel } from "../models/payment";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class PaymentService extends AppClientService {
  constructor() {
    super();
  }

  GetAll() {
    return this.get<IResponse<IPaymentModel[]>>("Payment/GetAll");
  }
}
