import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPayVariable } from "../models/variables";
import { map } from "rxjs/operators";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class VariablesService extends AppClientService {
  constructor() {
    super();
  }

  GetAllByPaymentId(paymentId: number) {
    return this.get<IResponse<IPayVariable[]>>(
      "Payment/GetAll_Varaible_By_PaymentID",
      {
        params: {
          Payment_ID: paymentId,
        },
      }
    ).pipe(
      map((res: IResponse<IPayVariable[]>) => {
        res.data.map((ele: IPayVariable) => {
          ele.ar_Name = ele.arName;
          ele.en_Name = ele.name;
        });
        return res;
      })
    );
  }
}
