import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { ILoanTypeModel, ILoanTypeRequest } from "../models/loan-types";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LoanTypesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILoanTypeRequest) {
    return this.post<IResponse<ILoanTypeModel>>("LoanTypeSetup/Create", data);
  }

  update(data: ILoanTypeRequest) {
    return this.post<IResponse<ILoanTypeModel>>("LoanTypeSetup/Edit", data);
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ILoanTypeModel[]>>("LoanTypeSetup/GetList", {
      params: {
        ...params,
        $expand: `${db.PAY_Loan_Type_Variable},${db.PAY_Loan_Type_Variable}/${db.PAY_Variable}`,
      },
    });
  }

  getAll() {
    return this.get<IResponse<ILoanTypeModel[]>>("LoanTypeSetup/GetAll");
  }

  Delete(Id: number) {
    return this.delete<IResponse>("LoanTypeSetup/Delete", {
      params: { Id: Id },
    });
  }
}
