import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { IGradeModel } from "app/org-management-setup/models/grades";
import { IJobFamilyModel } from "app/org-management-setup/models/job-families";
import {
  IJobProfileRequest,
  IJobTitleModel,
} from "app/org-management/model/job-title";
import { JobTitleService } from "app/org-management/services/job-profile/job-title.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { GradesService } from "app/org-management-setup/services/grades.service";
import { JobFamiliesService } from "app/org-management-setup/services/job-families.service";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";

@Component({
  selector: "app-add-job-profile",
  templateUrl: "./add-job-profile.component.html",
  styleUrls: ["./add-job-profile.component.scss"],
})
export class AddJobProfileComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  jobId: number = 0;

  jobFamilyList: IJobFamilyModel[] = [];
  parentJobTitleList: IJobTitleModel[] = [];
  gradeList: IGradeModel[] = [];
  selectedGrade: IGradeModel;

  constructor(
    private jobTitleService: JobTitleService,
    private jobFamiliesService: JobFamiliesService,
    private gradesService: GradesService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
		public dialogRef: MatDialogRef<AddJobProfileComponent>
  ) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.jobId = Number(params.get("jobId")) ?? 0;
    });

    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      code: [null, [Validators.required, CustomValidator.whiteSpace]],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      job_Family_ID: [null],
      parentJobTitleID: [null],
      grade_ID: [null],
    });
    if (this.data.value) {
			this.formCtrls.ar_Name.setValue(this.data.value);
			this.formCtrls.en_Name.setValue(this.data.value);
		}
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAllJobFamilies();
      this.getAllParentJobTitles();
      this.getAllgrades();
      if (this.jobId > 0) this.getById();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.jobTitleService
      .getById(this.jobId)
      .subscribe((res: IResponse<IJobTitleModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.jobProfileToEdit(res.data[0]);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  jobProfileToEdit(jobProfile: IJobTitleModel) {
    this.form.patchValue(jobProfile);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobProfileRequest = {
        job_title: {
          ID: this.form.value.id,
          ar_Name: this.form.value.ar_Name,
          en_Name: this.form.value.en_Name,
          code: this.form.value.code,
          job_Family_ID: this.form.value.job_Family_ID,
          parentJobTitleID: this.form.value.parentJobTitleID,
          grade_ID: this.form.value.grade_ID,
        },
        extraData: {},
      };

      model.job_title.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IJobProfileRequest) {
    this.showLoading();
    this.jobTitleService
      .create(model)
      .subscribe((res: IResponse<IJobTitleModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.jobId = res.data.id;
          this.formCtrls.id.setValue(this.jobId);
          let url = `/org-management/${this.moduleId}/job-profile/edit/${this.jobId}`;
          this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IJobProfileRequest) {
    this.showLoading();
    this.jobTitleService
      .update(model)
      .subscribe((res: IResponse<IJobTitleModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  // job Family List
  getAllJobFamilies() {
    this.showLoading();
    this.jobFamiliesService.getAll().subscribe((res) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        this.jobFamilyList = res.data;
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  // parent Job Title List
  getAllParentJobTitles() {
    this.showLoading();
    this.jobTitleService.getAllParentJobTitles(this.jobId).subscribe((res) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        this.parentJobTitleList = res.data;
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  // grade List
  getAllgrades() {
    this.showLoading();
    this.gradesService.getAll().subscribe((res) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        this.gradeList = res.data;
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  onChangeGrade(values: IGradeModel[]) {
    this.selectedGrade = values ? values[0] : null;
  }
  onCloseDialog() {
		this.dialogRef.close();
	}
}
