import { Injectable } from "@angular/core";
import { map } from "rxjs/operators";
import { AppClientService } from "app/shared/services/app-client.service";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import {
	IUserRequest,
	IUserModel,
	IUserPoliciesRequest,
	IGroupPolicies,
	IUserCompaniesRequest,
	IUserCompaniesModel,
	IUMUsers,
} from "../../models/users";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class UsersService extends AppClientService {
	constructor() {
		super();
	}
	create(data: IUserRequest) {
		return this.post<IResponse<IUserModel>>("GeneralSetup/User/Create", data);
	}

	update(data: IUserRequest) {
		return this.post<IResponse<IUserModel>>("GeneralSetup/User/Edit", data);
	}

	getUserById(id: number) {
		return this.get<IResponse<IUserModel[]>>("GeneralSetup/User/GetList", {
			params: {
				PageNumber: 1,
				pageSize: 1,
				$filter: `user_ID eq ${id}`,
				$expand: `${db.UM_Users}/${db.uM_Users_Groups},${db.Core_Person_Instance}`,
			},
		});
	}

	getList(pageParams: IPageParams) {
		return this.get<IResponseList<IUserModel[]>>("GeneralSetup/User/GetList", {
			params: {
				...pageParams,
				$expand: `${db.UM_Users}/${db.uM_Users_Groups},${db.Core_Person_Instance}`,
			},
		});
	}

	GetAll() {
		return this.get<IResponse<IUMUsers[]>>("GeneralSetup/User/GetAll").pipe(
			map((res) => {
				res.data.map((item: IUMUsers) => {
					item.ar_Name = item.name;
					item.en_Name = item.name;
				});
				return res;
			})
		);
	}

	Delete(id: number) {
		return this.delete<IResponse>("GeneralSetup/User/Delete", {
			params: { id: id },
		});
	}

	//----------------- User Groups has an individual service -------------------
}
