import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";

@Component({
	selector: "reset-form-btn",
	templateUrl: "./reset-form-btn.component.html",
	styleUrls: ["./reset-form-btn.component.scss"],
})
export class ResetFormBtnComponent implements OnInit {
	@Input() class?: any;
	@Output() click? = new EventEmitter<any>();

	constructor() {}

	ngOnInit(): void {}
	onClick($event) {
		this.click.emit($event);
	}
}
