import { Component, EventEmitter, Input, Output, SimpleChanges } from "@angular/core";
import { FormControl } from "@angular/forms";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { LanguageService } from "app/shared/services/language.service";

@Component({
	selector: "app-input",
	templateUrl: "./input.component.html",
	styleUrls: ["./input.component.scss"],
})
export class InputComponent {
	@Input() control: FormControl;
	@Input() label: string;
	@Input() type?: string = "text";
	@Input() placeholder?: string = "";
	@Input() submitted?: boolean = false;
	@Input() disabled?: boolean = false;
	@Input() readonly?: boolean = false;
	@Input() appearance?: string = Constants.defaultInputAppearance;
	@Input() class?: string;
	@Input() id?: string;
	@Input() step?: number;
	@Input() autocomplete?: string;
	@Input() matAutocomplete?: any = null;
	@Output() keyUp? = new EventEmitter<any>();
	@Output() keyDown? = new EventEmitter<any>();
	@Output() keyPress? = new EventEmitter<any>();
	@Output() keyEnter? = new EventEmitter<any>();
	@Output() change? = new EventEmitter<any>();

	constructor(public languageService: LanguageService) {}

	ngOnChanges(changes: SimpleChanges): void {
		if (changes.disabled?.currentValue) this.control?.disable();
		else if (!changes.disabled?.currentValue) this.control?.enable();
		//	else this.control?.enable();
	}

	onkeyup(value: any) {
		this.keyUp.emit(value);
	}

	onkeydown(value: any) {
		this.keyDown.emit(value);
	}

	onkeypress(value: any) {
		this.keyPress.emit(value);
	}

	onkeyEnter(value: any) {
		this.keyEnter.emit(value);
	}
	onChange(value: any) {
		this.change.emit(value);
	}
}
