import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { ICompetenceLevelDetailsModel, ICompetenceModel } from "../model/competence";

@Injectable({
  providedIn: "root",
})
export class CompetenceService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<ICompetenceModel[]>>(
      "OrgManagement/Competence/GetAll"
    );
  }

  // competence details
  getDetailsByCompetenceAndLevelIds(
    competenceId: number,
    competenceLevelId: number
  ) {
    return this.get<IResponse<ICompetenceLevelDetailsModel[]>>(
      "OrgManagement/Competence/GetAll_CompetenceDetail",
      {
        params: {
          $filter: `Competence_ID eq ${competenceId} and Competence_Level_ID eq ${competenceLevelId}`,
        },
      }
    );
  }
}
