import { Injectable } from "@angular/core";
import {
  ICompetenceLevelModel,
  ICompetenceLevelRequest,
} from "app/org-management-setup/models/competence-level";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CompetenceLevelService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICompetenceLevelRequest) {
    return this.post<IResponse<ICompetenceLevelModel>>(
      "CompetenceLevel/Create",
      data
    );
  }

  update(data: ICompetenceLevelRequest) {
    return this.post<IResponse<ICompetenceLevelModel>>(
      "CompetenceLevel/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ICompetenceLevelModel[]>>(
      "CompetenceLevel/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<ICompetenceLevelModel[]>>(
      "CompetenceLevel/GetAll"
    );
  }

  getAllByCompetenceId(competenceId: number) {
    return this.get<IResponse<ICompetenceLevelModel[]>>(
      "CompetenceLevel/GetAll",
      {
        params: {
          $filter: `Core_Competence_Detail/any(e: e/Competence_ID eq ${competenceId})`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("CompetenceLevel/Delete", {
      params: { Id: Id },
    });
  }
}
