import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IKpiRequest, IKpiModel } from "../models/kpi";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class KpiService extends AppClientService {
	constructor() {
		super();
	}
	create(data: IKpiRequest) {
		return this.post<IResponse<IKpiModel>>("HRSetup/kpi/Create", data);
	}

	update(data: IKpiRequest) {
		return this.post<IResponse<IKpiModel>>("HRSetup/kpi/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IKpiModel[]>>("HRSetup/KPI/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IKpiModel[]>>("HRSetup/kpi/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/kpi/Delete", {
			params: { Id: id },
		});
	}
}
