import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IBankModel, IBankRequest } from "../../models/bank";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class BankService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IBankRequest) {
		return this.post<IResponse<IBankModel>>("HRSetup/bank/Create", data);
	}

	update(data: IBankRequest) {
		return this.post<IResponse<IBankModel>>("HRSetup/bank/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IBankModel[]>>("HRSetup/bank/GetList", {
			params: params,
		});
	}

	getById(id: number) {
		return this.get<IResponse<IBankModel[]>>("HRSetup/Bank/GetAll", {
			params: {
				$filter: `id eq ${id}`,
			},
		});
	}

	getAll() {
		return this.get<IResponse<IBankModel[]>>("HRSetup/bank/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/bank/Delete", {
			params: { Id: id },
		});
	}
}
