import { IGlAccountModel } from "./../models/s4-account-map";
import { IResponse } from "app/shared/interfaces/responces";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class GlAccountService extends AppClientService {
  constructor() {
    super();
  }

  getAllByS4SystemInstnaceId(s4SystemInstnaceId: number) {
    return this.get<IResponse<IGlAccountModel[]>>(
      "S4AccountMapSetup/GetAll_GlAccount",
      {
        params: {
          SysteminstanceId: s4SystemInstnaceId,
        },
      }
    ).pipe(
      map((res) => {
        res.data.map((item: IGlAccountModel) => {
          item.id = item.Gl;
          item.ar_Name = item.Gl + " - " + item.GLName;
          item.en_Name = item.Gl + " - " + item.GLName;
        });
        return res;
      })
    );
  }
}
