import { Component, OnInit, Input } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { CustomValidators } from "ngx-custom-validators";
import {
  IJobAppraisalMethodModel,
  IJobAppraisalMethodRequest,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { OrganizationsService } from "app/org-management-setup/services/organizations.service";
import { IOrganizationModel } from "app/org-management-setup/models/organizations";
import { JobAppraisalMethodService } from "app/org-management/services/job-profile/job-appraisal-method.service";

@Component({
  selector: "app-job-appraisal-method",
  templateUrl: "./job-appraisal-method.component.html",
  styleUrls: ["./job-appraisal-method.component.scss"],
})
export class JobAppraisalMethodComponent extends AppHelpers implements OnInit {
  @Input() jobId: number;

  form: FormGroup;
  submitted: boolean = false;

  organizationList: IOrganizationModel[] = [];

  constructor(
    private jobAppraisalMethodService: JobAppraisalMethodService,
    private organizationsService: OrganizationsService
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      managerPercentage: [
        null,
        [CustomValidators.gte(0), CustomValidators.lte(100)],
      ],
      internalColleaguePercentage: [
        null,
        [CustomValidators.gte(0), CustomValidators.lte(100)],
      ],
      externalColleaguePercentage: [
        null,
        [CustomValidators.gte(0), CustomValidators.lte(100)],
      ],
      relatedOrgJobTitle: [null],
      subordinatePercentage: [
        null,
        [CustomValidators.gte(0), CustomValidators.lte(100)],
      ],
      mySelfPercentage: [
        null,
        [CustomValidators.gte(0), CustomValidators.lte(100)],
      ],
      customerPercentage: [
        null,
        [CustomValidators.gte(0), CustomValidators.lte(100)],
      ],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAllOrganizations();
      if (this.jobId > 0) this.getAll();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.showLoading();
    this.jobAppraisalMethodService
      .getAllByJobTitleId(this.jobId)
      .subscribe((res: IResponse<IJobAppraisalMethodModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.form.patchValue(res.data.job_Title);
          this.formCtrls.relatedOrgJobTitle.setValue(
            res.data.relatedOrgJobTitle
          );
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobAppraisalMethodRequest = {
        ExtraaJobttile: {
          ID: this.jobId,
          managerPercentage: this.form.value.managerPercentage,
          internalColleaguePercentage:
            this.form.value.internalColleaguePercentage,
          externalColleaguePercentage:
            this.form.value.externalColleaguePercentage,
          subordinatePercentage: this.form.value.subordinatePercentage,
          mySelfPercentage: this.form.value.mySelfPercentage,
          customerPercentage: this.form.value.customerPercentage,
        },
      };
      if (model.ExtraaJobttile.externalColleaguePercentage > 0)
        model.RelatedOrgJobTitle = this.form.value.relatedOrgJobTitle;

      // check if sum of percentages = 100
      let sum = 0;
      for (let key in model.ExtraaJobttile) {
        if (key != "ID") sum += model.ExtraaJobttile[key] ?? 0;
      }
      if (sum == 100) this.create(model);
      else this.warning("SummationOfPercentageError");
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IJobAppraisalMethodRequest) {
    this.showLoading();
    this.jobAppraisalMethodService
      .create(model)
      .subscribe((res: IResponse<IJobAppraisalMethodModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.getAll();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  // organizations list
  getAllOrganizations() {
    this.showLoading();
    this.organizationsService.getAll().subscribe((res) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        this.organizationList = res.data;
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }
}
