import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IServiceModel } from "../models/service";
import { map } from "rxjs/operators";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class ServiceService extends AppClientService {
  constructor() {
    super();
  }

  GetAll(params) {
    return this.get<IResponse<IServiceModel[]>>("UmServices/GetAll", {
      params: params,
    }).pipe(
      map((res: IResponse<IServiceModel[]>) => {
        res.data.map((item: IServiceModel) => {
          item.ar_Name = item.arbName;
          item.en_Name = item.engName;
        });
        return res;
      })
    );
  }

  GetList(params) {
    return this.get<IResponseList<IServiceModel[]>>("UmServices/GetList", {
      params: params,
    });
  }
}
