import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import { IGlAccountModel } from "app/s4-setup/models/s4-account-map";
import { GlAccountService } from "app/s4-setup/services/gl-account.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-gl-account-by-s4-system-instance-dropdown",
	templateUrl: "./gl-account-by-s4-system-instance-dropdown.component.html",
	styleUrls: ["./gl-account-by-s4-system-instance-dropdown.component.scss"],
})
export class GlAccountByS4SystemInstanceDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() s4SystemInstanceControl: FormGroup;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "glAccount";
	@Output() change = new EventEmitter<IGlAccountModel[]>();

	glAccountList: IGlAccountModel[] = [];

	constructor(private glAccountService: GlAccountService) {
		super();
	}

	ngOnInit(): void {
		this.control.disable();

		this.s4SystemInstanceControl?.valueChanges.subscribe((s4SystemInstanceId) => {
			this.control.setValue(null);

			if (s4SystemInstanceId > 0) {
				this.getAllIntegrationPurposesBySystemInstance();
				this.control.enable();
			} else this.control.disable();
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.s4SystemInstanceControl?.value > 0 && this.checkNoBusinessOrCompany())
			this.getAllIntegrationPurposesBySystemInstance();
	}

	getAllIntegrationPurposesBySystemInstance() {
		this.showLoading();
		this.glAccountService
			.getAllByS4SystemInstnaceId(this.s4SystemInstanceControl?.value)
			.subscribe((res: IResponse<IGlAccountModel[]>) => {
				if ((res.message.messageType = MessageTypes.Success)) {
					this.glAccountList = res.data;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	onChange(glAccount: IGlAccountModel[]) {
		this.change.emit(glAccount);
	}
}
