import { StringsManager } from "app/shared/resources/strings-manager";
import { JwtHelperService } from "@auth0/angular-jwt";

export class Storage {
	static appStrings: StringsManager = new StringsManager();
	static jwtHelper: JwtHelperService = new JwtHelperService();

	static setItem(key: string, value: any) {
		localStorage.setItem(key, value);
	}

	static getItem(key: string) {
		return localStorage.getItem(key);
	}

	static removeItem(key: string) {
		localStorage.removeItem(key);
	}

	static customClearStorag(keyToException: string) {
		Object.keys(localStorage).forEach((key) => {
			if (!key.includes(keyToException)) delete localStorage[key];
		});
	}

	// ==========================================================================================================================
	// Start Set & get [Language]
	static setLocalLang(lang: string) {
		this.setItem(this.appStrings.CURRENT_LANG, lang);
	}

	static get getLocalLang() {
		return this.getItem(this.appStrings.CURRENT_LANG);
	}

	// Start set & get [TOKEN] ==============================================
	static setToken(token: string) {
		this.setItem(this.appStrings.TOKEN, token);
	}

	static get getToken() {
		return this.getItem(this.appStrings.TOKEN ?? null);
	}

	// Start set & get [user type id] ===========================================
	static setUserTypeId(user_type_id: any) {
		this.setItem(this.appStrings.USER_TYPE_ID, user_type_id);
	}

	static get getUserTypeId(): number {
		return Number(this.getItem(this.appStrings.USER_TYPE_ID));
	}

	// Start set & get [UserId] ===========================================
	// static setUserId(UserId: number) {
	//   this.setItem(this.appStrings.USER_ID, UserId);
	// }

	static get getUserId(): number {
		return this.jwtHelper.decodeToken(Storage.getToken)?.UID ?? null;
	}

	// Start set & get [isUser] ===========================================
	// static setIsUserLoggedIn(isLoggedIn: boolean) {
	//   this.setItem(this.appStrings.IsLoggedIn, isLoggedIn);
	// }

	static get isUserLoggedIn(): boolean {
		return Boolean(this.getToken ? true : false);
	}

	// Start set & get [selectedModule] ==============================================
	static setSelectedModule(modId) {
		this.setItem(this.appStrings.selectedModule, modId);
	}

	static get getSelectedModule(): number {
		return Number(this.getItem(this.appStrings.selectedModule ?? null));
	}

	static setParentModule(modId) {
		this.setItem(this.appStrings.parentModule, modId);
	}

	static get getParentModule(): number {
		return Number(this.getItem(this.appStrings.parentModule ?? null));
	}

	static removeSelectedModule() {
		this.removeItem(this.appStrings.selectedModule);
	}

	// Start set & get [businessID] ==============================================
	static setBusinessID(businessID: number) {
		this.setItem(this.appStrings.businessID, businessID);
	}

	static get getBusinessID(): number {
		return Number(this.getItem(this.appStrings.businessID ?? null));
	}

	// Start set & get [CompanyID] ==============================================
	static setCompanyID(CompanyID: number) {
		this.setItem(this.appStrings.companyID, CompanyID);
	}

	static get getCompanyID(): number {
		return Number(this.getItem(this.appStrings.companyID ?? null));
	}

	// Start set & get [ContractID] ==============================================
	static setContractID(contractID: number) {
		this.setItem(this.appStrings.contractID, contractID);
	}

	static get getContractID(): number {
		return Number(this.getItem(this.appStrings.contractID ?? null));
	}
}
