import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IGroupPolicies } from "app/general-setup/models/users";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { PoliceiesByCompanyService } from "app/general-setup/services/users/policeies-by-company.service";

@Component({
	selector: "policy-groups-by-company-dropdown",
	templateUrl: "./policy-groups-dropdown.component.html",
	styleUrls: ["./policy-groups-dropdown.component.scss"],
})
export class PolicyGroupsByCompanyDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "policyGroups";
	@Input() companyCtrl?: FormControl;
	policyGroupList: IGroupPolicies[];

	constructor(private userPoliciesService: PoliceiesByCompanyService) {
		super();
	}

	ngOnInit(): void {}

	ngOnChanges() {
		this.companyCtrl?.valueChanges.subscribe((companyId) => {
			if (companyId > 0) {
				this.getPoliceiesByCompany();
			} else {
				this.policyGroupList = [];
			}
		});
	}
	getPoliceiesByCompany() {
		const params = {
			compId: this.companyCtrl.value,
		};
		this.showLoading();

		this.userPoliciesService
			.getPoliceiesByCompany(params)
			.subscribe((res: IResponse<IGroupPolicies[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.policyGroupList = res.data;
				} else {
					this.errorList(res.message.messages);
				}
			});
	}
}
