import { DatePipe } from "@angular/common";
import { Languages } from "../enums/languages.enum";
import { Storage } from "./local-storage";

// @Injectable({
// 	providedIn: "root",
// })
export class HelpersFunctions {
	static datePipe = new DatePipe("en-US");

	static getMatched(data: any[], matchedProp: string) {
		var filte: any[] = data
			.map((item) => item[matchedProp])
			.filter((name, index, currentVal) => currentVal.indexOf(name) === index);
		return filte;
	}

	static mapDate(date: string) {
		return this.datePipe.transform(date, "dd-MM-yyyy");
	}

	// get day name by date getDayName('2022-01-29') => Saturday
	static getDayName(date = new Date(), locale = "en-US") {
		return date.toLocaleDateString(locale, { weekday: "long" });
	}

	static mapDateReverse(date: string) {
		return this.datePipe.transform(date, "yyyy-MM-dd");
	}

	static mapTime(time: string) {
		if (time) {
			let timeParts = time.split(":");
			let date = new Date(
				null,
				null,
				null,
				Number(timeParts[0]),
				Number(timeParts[1] ?? 0),
				Number(timeParts[2] ?? 0)
			);
			return this.datePipe.transform(date, "hh:mm a");
		}
	}
}

export function msgTranslate(arProp: string, enProp?: string) {
	return Storage.getLocalLang == Languages.ar ? arProp ?? enProp : enProp ?? arProp;
}
