import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  ViewChildren,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { Router } from "@angular/router";
import { IBusinessModel, IBusinessResponse, ICompanyModel } from "app/auth/models/business-company";
import { AuthService } from "app/auth/services/auth.service";
import { Subscription } from "rxjs";
import { AssetsManager } from "../resources/assets-manager";
import { StringsManager } from "../resources/strings-manager";
import { Languages } from "../enums/languages.enum";
import { Constants } from "../resources/constants";
import { LanguageService } from "../services/language.service";
import { ParamsService } from "../services/params.service";
import { ConfigService } from "../services/theme-options/config.service";
import { LayoutService } from "../services/theme-options/layout.service";

@Component({
	selector: "app-navbar",
	templateUrl: "./navbar.component.html",
	styleUrls: ["./navbar.component.scss"],
})
export class NavbarComponent implements OnInit, AfterViewInit, OnDestroy {
	public appAssets: AssetsManager = new AssetsManager();
	public appStrings: StringsManager = new StringsManager();
	selectedLanguageText = this.appStrings.english;
	selectedLanguageFlag = this.appAssets.enFlag;
	toggleClass = this.appStrings.toggleClass;
	placement = this.appStrings.placement;
	logoUrl = this.appAssets.logo;
	menuPosition = this.appStrings.menuPosition;
	isSmallScreen = false;
	protected innerWidth: any;
	searchOpenClass = "";
	transparentBGClass = "";
	hideSidebar: boolean = true;
	public isCollapsed = true;
	layoutSub: Subscription;
	configSub: Subscription;

	@ViewChild("search") searchElement: ElementRef;
	@ViewChildren("searchResults") searchResults: QueryList<any>;

	@Output()
	toggleHideSidebar = new EventEmitter<Object>();

	@Output()
	seachTextEmpty = new EventEmitter<boolean>();

	listItems = [];
	control = new FormControl();

	public config: any = {};

	businessList: IBusinessModel[];
	companyList: ICompanyModel[];
	curBusinessId: number;
	currentCompanyId: number;
	appearance = Constants.defaultInputAppearance;

	constructor(
		private layoutService: LayoutService,
		private router: Router,
		private configService: ConfigService,
		public languageService: LanguageService,
		private authService: AuthService,
		private paramsService: ParamsService,
		private cdr: ChangeDetectorRef
	) {
		this.config = this.configService.templateConf;
		this.innerWidth = window.innerWidth;

		this.layoutSub = layoutService.toggleSidebar$.subscribe((isShow) => {
			this.hideSidebar = !isShow;
		});

		if (languageService.isArabic) {
			this.selectedLanguageText = this.appStrings.arabic;
			this.selectedLanguageFlag = this.appAssets.arFlag;
		}
		if (languageService.isEnglish) {
			this.selectedLanguageText = this.appStrings.english;
			this.selectedLanguageFlag = this.appAssets.enFlag;
		}

		// this.curBusinessId = Storage.getBusinessID;
		// this.currentCompanyId = Storage.getCompanyID;

		this.paramsService.businessCompanyParams$.subscribe((val) => {
			this.curBusinessId = val.BusinessEntityID;
			this.currentCompanyId = val.CompanyID;
		});

		this.authService.getCompanyAndBusiness().subscribe((res: IBusinessResponse) => {
			res.data.map((e) => {
				e.id = e.businessId;
			});
			this.businessList = res.data;
			this.authService.BusinessList$.next(res.data);
			if (!this.curBusinessId) this.onSelectBusiness(res.data[0]?.businessId);
			if (!this.currentCompanyId) this.onSelectCompany(res.data[0]?.companies[0].id);
			if (this.currentCompanyId) this.getCompaniesBybusinessId(this.curBusinessId);
			else this.getCompaniesBybusinessId(res.data[0]?.companies[0].id);
		});
	}

	ngOnInit() {
		this.listItems = [
			{ url: "/page", name: "Page", icon: "ft-home" },
			{ url: "/page", name: "Pages", icon: "ft-home" },
			{ url: "/pages/login", name: "Login", icon: "ft-log-in" },
		];

		if (this.innerWidth < 1200) {
			this.isSmallScreen = true;
		} else {
			this.isSmallScreen = false;
		}
	}

	ngAfterViewInit() {
		this.configSub = this.configService.templateConf$.subscribe((templateConf) => {
			if (templateConf) {
				this.config = templateConf;
			}
			this.loadLayout();
			this.cdr.markForCheck();
		});
	}

	getCompaniesBybusinessId(businessId: number) {
		//	console.log("getCompaniesBybusinessId", businessId);

		let index = this.businessList?.findIndex((e) => e.businessId == businessId);
		this.companyList = this.businessList[index]?.companies ?? [];
		if (this.companyList?.length > 0) {
			this.onSelectCompany(this.companyList[0].id);
		}
	}

	onSelectBusiness(businessId: number) {
		this.paramsService.onSelectBusiness(businessId);
		this.paramsService.onSelectCompany(null);
		//	this.curBusinessId = businessId;
		this.getCompaniesBybusinessId(businessId);
	}

	onSelectCompany(companyId: number) {
		//	this.currentCompanyId = companyId;
		this.paramsService.onSelectCompany(companyId);
	}

	logout() {
		this.authService.logout();
	}

	ChangeLanguage(language: string) {
		if (language != this.languageService.currentLang) {
			location.reload();
			this.languageService.changeLanguage(language);
			if (language === Languages.en) {
				Promise.resolve().then(() => {
					this.selectedLanguageText = this.appStrings.english;
					this.selectedLanguageFlag = this.appAssets.enFlag;
				});
			} else if (language === Languages.ar) {
				Promise.resolve().then(() => {
					this.selectedLanguageText = this.appStrings.arabic;
					this.selectedLanguageFlag = this.appAssets.arFlag;
				});
			}
		}
	}

	// //////////////////////////////////////////////////

	@HostListener("window:resize", ["$event"])
	onResize(event) {
		this.innerWidth = event.target.innerWidth;
		if (this.innerWidth < 1200) {
			this.isSmallScreen = true;
		} else {
			this.isSmallScreen = false;
		}
	}

	loadLayout() {
		if (
			this.config.layout.menuPosition &&
			this.config.layout.menuPosition.toString().trim() != ""
		) {
			this.menuPosition = this.config.layout.menuPosition;
		}

		if (this.config.layout.variant === "Light") {
			this.logoUrl = this.appAssets.logo;
		} else {
			this.logoUrl = this.appAssets.darkLogo;
		}

		if (this.config.layout.variant === "Transparent") {
			this.transparentBGClass = this.config.layout.sidebar.backgroundColor;
		} else {
			this.transparentBGClass = "";
		}
	}

	onSearchKey(event: any) {
		if (this.searchResults && this.searchResults.length > 0) {
			this.searchResults.first.host.nativeElement.classList.add("first-active-item");
		}

		if (event.target.value === "") {
			this.seachTextEmpty.emit(true);
		} else {
			this.seachTextEmpty.emit(false);
		}
	}

	removeActiveClass() {
		if (this.searchResults && this.searchResults.length > 0) {
			this.searchResults.first.host.nativeElement.classList.remove("first-active-item");
		}
	}

	onEscEvent() {
		this.control.setValue("");
		this.searchOpenClass = "";
		this.seachTextEmpty.emit(true);
	}

	onEnter() {
		if (this.searchResults && this.searchResults.length > 0) {
			let url = this.searchResults.first.url;
			if (url && url != "") {
				this.control.setValue("");
				this.searchOpenClass = "";
				this.router.navigate([url]);
				this.seachTextEmpty.emit(true);
			}
		}
	}

	redirectTo(value) {
		this.router.navigate([value]);
		this.seachTextEmpty.emit(true);
	}

	ToggleClass() {
		if (this.toggleClass === "ft-maximize") {
			this.toggleClass = "ft-minimize";
		} else {
			this.toggleClass = "ft-maximize";
		}
	}

	toggleSearchOpenClass(display) {
		this.control.setValue("");
		if (display) {
			this.searchOpenClass = "open";
			setTimeout(() => {
				this.searchElement.nativeElement.focus();
			}, 0);
		} else {
			this.searchOpenClass = "";
		}
		this.seachTextEmpty.emit(true);
	}

	toggleNotificationSidebar() {
		this.layoutService.toggleNotificationSidebar(true);
	}

	toggleSidebar() {
		this.layoutService.toggleSidebarSmallScreen(this.hideSidebar);
	}

	ngOnDestroy() {
		if (this.layoutSub) {
			this.layoutSub.unsubscribe();
		}
		if (this.configSub) {
			this.configSub.unsubscribe();
		}
	}
}
