import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { EmployeeGeneralInfoDialogComponent } from "app/personnel-setup/components/employees/employee-general-info/emp-general-info-dialog/emp-general-info-dialog.component";
import { IEmployeeModel } from "app/personnel-setup/models/employees";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
	selector: "app-employee-dropdown",
	templateUrl: "./employee-dropdown.component.html",
	styleUrls: ["./employee-dropdown.component.scss"],
})
export class EmployeeDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() appendTo: string = "body";
	@Input() label: string = "employees";
	@Output() change = new EventEmitter<IEmployeeModel[]>();
	viewName: string;
	employeeList: IEmployeeModel[] = [];

	constructor(private employeeServ: EmployeesService) {
		super();
    // add parent viewname beacuse it does  not have route

		this.viewName = ViewsManager.add_user;
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllEmployees();
	}

	getAllEmployees() {
		this.showLoading();
		this.employeeServ.getAll().subscribe((res: IResponse<IEmployeeModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				res.data.map((emp) => {
					this.employeeList.push(emp);
				});
				this.employeeList = res.data;
			} else this.errorList(res.message?.messages);
			this.hideLoading();
		});
	}

	onChange(jobTitle: IEmployeeModel[]) {
		this.change.emit(jobTitle);
	}
	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(EmployeeGeneralInfoDialogComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllEmployees();
			});
	}
}
