import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IAttachmenModel } from "../models/attachments";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
	providedIn: "root",
})
export class AttachmentService extends AppClientService {
	constructor() {
		super();
	}

	getAll() {
		return this.get<IResponse<IAttachmenModel[]>>("GeneralSetup/AttachmentType/GetAll");
	}
}
