import { Component, Inject, OnInit } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CompetenceService } from "app/org-management/services/competence.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICompetenceLevelDetailsModel } from "app/org-management/model/competence";

@Component({
  selector: "app-job-competence-level-details-dialog",
  templateUrl: "./job-competence-level-details-dialog.component.html",
  styleUrls: ["./job-competence-level-details-dialog.component.scss"],
})
export class JobCompetenceLevelDetailsDialogComponent
  extends AppHelpers
  implements OnInit
{
  competenceId: number;
  competencelevelId: number;
  levelDetails: ICompetenceLevelDetailsModel;

  constructor(
    private competenceService: CompetenceService,
    public dialogRef: MatDialogRef<JobCompetenceLevelDetailsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    super();
    this.competenceId = data.competenceId;
    this.competencelevelId = data.competencelevelId;
  }

  ngOnInit(): void {
    if (
      this.checkNoBusinessOrCompany() &&
      this.competenceId > 0 &&
      this.competencelevelId > 0
    )
      this.getDetails();
  }

  getDetails() {
    this.competenceService
      .getDetailsByCompetenceAndLevelIds(
        this.competenceId,
        this.competencelevelId
      )
      .subscribe((res) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          // [this.levelDetails] = res.data.slice(-1);
          this.levelDetails = res.data[0];
        } else this.errorList(res.message.messages);
      });
  }
}
