import { Component, Input } from "@angular/core";
import { FormControl } from "@angular/forms";
import { Constants } from "app/shared/resources/constants";

@Component({
	selector: "input-validation-msg",
	templateUrl: "./input-validation-msg.component.html",
	styleUrls: ["./input-validation-msg.component.scss"],
})
export class InputValidationMsgComponent {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() isSelect?: boolean = false;
	@Input() isDate?: boolean = false;
	@Input() isFile?: boolean = false;
	mobileLength: number;
	mobileStartsWith: string;

	constructor() {
		this.mobileLength = Constants.mobileLength;
		this.mobileStartsWith = Constants.mobileStartsWith;
	}

	// get control name (equalTo & notEqualTo validators)
	getControlName(validatorName: string): string | null {
		const parent = this.control.errors[validatorName].control.parent;
		if (parent) {
			const controls = parent.controls;
			const controlName = Object.keys(controls).find(
				(name) => controls[name].value === this.control.errors[validatorName].value
			);
			return controlName || null;
		}
		return null;
	}
}
