import { Injectable } from "@angular/core";
import { IGradeModel, IGradeRequest } from "app/org-management-setup/models/grades";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class GradesService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IGradeRequest) {
		return this.post<IResponse<IGradeModel>>("Grade/Create", data);
	}

	update(data: IGradeRequest) {
		return this.post<IResponse<IGradeModel>>("Grade/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IGradeModel[]>>("Grade/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IGradeModel[]>>("Grade/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("Grade/Delete", {
			params: { Id: id },
		});
	}
}
