import { Injectable } from "@angular/core";
import {
  ISystemInstanceModel,
  ISystemInstanceRequest,
} from "app/sap-business-one-setup/models/system-instance";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class SystemInstanceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISystemInstanceRequest) {
    return this.post<IResponse<ISystemInstanceModel>>(
      "SystemInstanceS1Setup/Create",
      data
    );
  }

  update(data: ISystemInstanceRequest) {
    return this.post<IResponse<ISystemInstanceModel>>(
      "SystemInstanceS1Setup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ISystemInstanceModel[]>>(
      "SystemInstanceS1Setup/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponseList<ISystemInstanceModel[]>>(
      "SystemInstanceS1Setup/GetAll"
    );
  }

  getById(id: number) {
    return this.get<IResponse<ISystemInstanceModel[]>>(
      "SystemInstanceS1Setup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SystemInstanceS1Setup/Delete", {
      params: { Id: Id },
    });
  }
}
