import {
  ChangeDetectionStrategy,
  Component,
  Injector,
  OnInit,
  Inject,
} from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { Params } from "@angular/router";
import {
  IUMUsers,
  IUserModel,
  IUserRequest,
} from "app/general-setup/models/users";
import { UsersService } from "app/general-setup/services/users/users.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";

@Component({
  selector: "app-add-user",
  templateUrl: "./add-user.component.html",
  styleUrls: ["./add-user.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AddUserComponent extends AppHelpers implements OnInit {
  userId: number = 0;
  form: FormGroup;
  submitted: boolean = false;
  userGroupsIds: number[] = [];
  username: string;
  constructor(
    private userService: UsersService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddUserComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params: Params) => {
      this.userId = Number(params.userId) ?? 0;

      // if (this.CheckNoBusinessOrCompany() && this.userId > 0) {
      // 	this.getUserById();
      // }
    });

    this.form = this.fb.group({
      Personid: [null, [Validators.required]],
      id: [this.userId, { nonNallable: true }],
      name: [null, [Validators.required]],
      passWord: [null, [Validators.required]],
      is_Active: [false, [Validators.required]],
    });
    if (this.data.value) {
      this.formCtrls.name.setValue(this.data.value);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.userId > 0) this.getUserById();
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IUserRequest = {
        core_Person_Instance: {
          id: this.formCtrls.Personid.value,
        },
        uM_Users: {
          id: this.formCtrls.id.value,
          name: this.formCtrls.name.value,
          passWord: this.formCtrls.passWord.value,
          is_Active: this.formCtrls.is_Active.value,
        },
      };
      model.uM_Users.id == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: IUserRequest) {
    this.showLoading();
    this.userService.create(model).subscribe((res: IResponse<IUserModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.addSuccessfullyMsg(res.message.messageTypeName);
        this.userId = res.data.user_ID;
        this.formCtrls.id.setValue(res.data.user_ID);
      } else this.errorList(res.message.messages);
    });
  }

  update(model: IUserRequest) {
    this.showLoading();
    this.userService.update(model).subscribe((res: IResponse<IUserModel>) => {
      if (res.message.messageType == MessageTypes.Success)
        this.updateSuccessfullyMsg(res.message.messageTypeName);
      else this.errorList(res.message.messages);
    });
  }

  getUserById() {
    this.userService
      .getUserById(this.userId)
      .subscribe((res: IResponseList<IUserModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          var user = res.data[0];
          this.form.patchValue(user.uM_Users);
          this.formCtrls.Personid.setValue(user.core_Person_Instance.id);
          this.username = user.uM_Users.name;
          //pushing ID of user`s group
          user.uM_Users.uM_Users_Groups.map((e) => {
            this.userGroupsIds.push(e.groupID);
          });
        }
      });
  }
  clearForm() {
    this.form.reset({ id: 0, userId: this.userId });
    this.submitted = false;
  }
  onCloseDialog() {
		this.dialogRef.close();
	}
}
