import { Injectable } from "@angular/core";
import { IJobFamilyRequest, IJobFamilyModel } from "app/org-management-setup/models/job-families";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class JobFamiliesService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IJobFamilyRequest) {
		return this.post<IResponse<IJobFamilyModel>>("JobFamily/Create", data);
	}

	update(data: IJobFamilyRequest) {
		return this.post<IResponse<IJobFamilyModel>>("JobFamily/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IJobFamilyModel[]>>("JobFamily/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IJobFamilyModel[]>>("JobFamily/GetAll");
	}

	getAllByOrgId(organizationId: number) {
		return this.get<IResponse<IJobFamilyModel[]>>("JobFamily/GetAll_JobFamilyByOrgID", {
			params: {
				Id: organizationId,
			},
		});
	}

	Delete(Id: number) {
		return this.delete<IResponse>("jobfamily/Delete", {
			params: { Id: Id },
		});
	}
}
