import { Component, OnInit, Input } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
	IJobCareerPathRequest,
	IJobCareerPathModel,
	IJobTitleModel,
	IJobCareerPathDeleteRequest,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { JobCareerPathService } from "app/org-management/services/job-profile/job-career-path.service";
import { CustomValidators } from "ngx-custom-validators";
import { MatTableDataSource } from "@angular/material/table";

@Component({
	selector: "app-job-career-path",
	templateUrl: "./job-career-path.component.html",
	styleUrls: ["./job-career-path.component.scss"],
})
export class JobCareerPathComponent extends AppHelpers implements OnInit {
	@Input() jobId: number;

	form: FormGroup;
	submitted: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		"nextJobTitle",
		"yeaR_NO",
		"pmS_VALUE",
		"iS_MANDATORY",
		"actions",
	];
	optionalColomns: string[] = ["responsabilities"];

	dataSource: MatTableDataSource<IJobCareerPathModel>;
	isLoaded: boolean = false;
	nextJobTitleList: IJobTitleModel[];

	constructor(private jobCareerPathService: JobCareerPathService) {
		super();
	}

	ngOnInit() {
		this.form = this.fb.group({
			joB_Title_NEXT_ID: [null, [Validators.required]],
			yeaR_NO: [null, [CustomValidators.digits, CustomValidators.gte(0)]],
			pmS_VALUE: [null, [CustomValidators.digits, CustomValidators.gte(0)]],
			iS_MANDATORY: [null],
			responsabilities: [null],
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllNextJobTitles();
			if (this.jobId > 0) this.getAll();
		}
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
		this.jobCareerPathService
			.getAllByJobTitleId(this.jobId)
			.subscribe((res: IResponse<IJobCareerPathModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((item: IJobCareerPathModel) => {
						item.nextJobTitle = this.msgTranslate(
							item.next_Job_Title?.ar_Name,
							item.next_Job_Title?.en_Name
						);
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IJobCareerPathRequest = {
				joB_Title_ID: this.jobId,
				joB_Title_NEXT_ID: this.form.value.joB_Title_NEXT_ID,
				yeaR_NO: this.form.value.yeaR_NO,
				pmS_VALUE: this.form.value.pmS_VALUE,
				iS_MANDATORY: this.form.value.iS_MANDATORY ?? false,
				responsabilities: this.form.value.responsabilities,
			};

			this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IJobCareerPathRequest) {
		this.showLoading();
		this.jobCareerPathService.create(model).subscribe((res: IResponse<IJobCareerPathModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				// update dropdown
				this.getAllNextJobTitles();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	onDelete(jobCareerPath: IJobCareerPathModel): void {
		let msg = this.msgTranslate(jobCareerPath.nextJobTitle);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(jobCareerPath);
		});
	}

	delete(jobCareerPath: IJobCareerPathModel) {
		let params: IJobCareerPathDeleteRequest = {
			NextJobId: jobCareerPath.joB_Title_NEXT_ID,
			JobId: jobCareerPath.joB_Title_ID,
		};
		this.showLoading();
		this.jobCareerPathService.Delete(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				// update dropdown
				this.getAllNextJobTitles();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	// next Job Title List
	getAllNextJobTitles() {
		this.showLoading();
		this.jobCareerPathService.getAllNextJobTitles(this.jobId).subscribe((res) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.nextJobTitleList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
