import {
	Component,
	ElementRef,
	EventEmitter,
	OnInit,
	Output,
	Renderer2,
	ViewChild,
} from "@angular/core";
import { LayoutService } from "app/shared/services/theme-options/layout.service";

@Component({
	selector: "side-helping-menu",
	templateUrl: "./side-helping-menu.component.html",
	styleUrls: ["./side-helping-menu.component.scss"],
})
export class SideHelpingMenuComponent implements OnInit {
	@ViewChild("helpingMenu") helpingMenu: ElementRef;
	size: string;
	isOpen = false;
	public config: any = {};

	constructor(private renderer: Renderer2, private layoutService: LayoutService) {}

	@Output() directionEvent = new EventEmitter<Object>();

	ngOnInit() {}

	toggleCustomizer() {
		if (this.isOpen) {
			this.renderer.removeClass(this.helpingMenu.nativeElement, "open");
			this.isOpen = false;
		} else {
			this.renderer.addClass(this.helpingMenu.nativeElement, "open");
			this.isOpen = true;
		}
	}

	closeCustomizer() {
		this.renderer.removeClass(this.helpingMenu.nativeElement, "open");
		this.isOpen = false;
	}
	onClose() {
		this.layoutService.toggleNotificationSidebar(false);
	}
}
