import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IAttachmenModel } from "app/general-setup/models/attachments";
import { AttachmentService } from "app/general-setup/services/attachment.service";
import {} from "app/hr-setup/models/hire-documents";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-attachment-dropdown",
	templateUrl: "./attachment-dropdown.component.html",
	styleUrls: ["./attachment-dropdown.component.scss"],
})
export class AttachmentDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean;
	@Input() multiple: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "attachments";

	attachmentsList: IAttachmenModel[];

	constructor(private attachmentService: AttachmentService) {
		super();
	}

	ngOnInit(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllAttachments();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllAttachments();
	}

	getAllAttachments() {
		this.showLoading();
		this.attachmentService.getAll().subscribe((res: IResponse<IAttachmenModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.attachmentsList = res.data;
			} else this.errorList(res.message?.messages);
			this.hideLoading();
		});
	}
}
