import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IOrganizationTypeModel } from "app/org-management-setup/models/organization-types";
import { OrganizationTypesService } from "app/org-management-setup/services/organization-types.service";
import { AddOrgStructureComponent } from "app/org-management/components/org-structure/add-org-structure/add-org-structure.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-org-types-dropdown-current-structure",
	templateUrl: "./org-types-dropdown-current-structure.component.html",
	styleUrls: ["./org-types-dropdown-current-structure.component.scss"],
})
export class OrgTypesDropdownCurrentStructureComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "organizationTypes";
	@Input() appendTo: string;
	@Output() change = new EventEmitter<IOrganizationTypeModel[]>();
	//viewName: string;

	organizationTypeList: IOrganizationTypeModel[];

	constructor(private organizationTypesService: OrganizationTypesService) {
		super();
		//this.viewName = ViewsManager.add_org_structure;
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getAllCurrentOrganizationTypes();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllCurrentOrganizationTypes();
	}

	getAllCurrentOrganizationTypes() {
		this.showLoading();
		this.organizationTypesService
			.getAllInCurrentStructure()
			.subscribe((res: IResponse<IOrganizationTypeModel[]>) => {
				if ((res.message.messageType = MessageTypes.Success)) {
					this.organizationTypeList = res.data;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	onChange(orgType: IOrganizationTypeModel[]) {
		this.change.emit(orgType);
	}
	// createDialog(value: string) {
	//   this.dialogsService
	//     .addFromDropdownDialog(AddOrgStructureComponent, value)
	//     .afterClosed()
	//     .subscribe((res) => {
	//       this.getAllCurrentOrganizationTypes();
	//     });
	// }
}
