import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { AddS4SystemInstanceComponent } from "app/s4-setup/components/s4-system-instance/add-s4-system-instance/add-s4-system-instance.component";
import { IS4SystemInstanceModel } from "app/s4-setup/models/s4-system-instance";
import { S4SytemInstanceService } from "app/s4-setup/services/s4-sytem-instance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-s4-system-instance-dropdown",
  templateUrl: "./s4-system-instance-dropdown.component.html",
  styleUrls: ["./s4-system-instance-dropdown.component.scss"],
})
export class S4SystemInstanceDropdownComponent
  extends AppHelpers
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "s4SystemInstance";
  @Input() appendTo: string = "body";
  @Output() change = new EventEmitter<IS4SystemInstanceModel[]>();
  viewName: string;

  s4SystemInstanceList: IS4SystemInstanceModel[];

  constructor(private s4SytemInstanceService: S4SytemInstanceService) {
    super();
    this.viewName = ViewsManager.add_s4_system_instance;
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany()) this.getAllS4SystemInstances();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllS4SystemInstances();
  }

  getAllS4SystemInstances() {
    this.showLoading();
    this.s4SytemInstanceService
      .getAll()
      .subscribe((res: IResponse<IS4SystemInstanceModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.s4SystemInstanceList = res.data;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddS4SystemInstanceComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllS4SystemInstances();
      });
  }

	onChange(items: IS4SystemInstanceModel[]) {
		this.change.emit(items);
	}
}
