import { Injectable } from "@angular/core";
import {
  IJobAppraisalMethodModel,
  IJobAppraisalMethodRequest,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class JobAppraisalMethodService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobAppraisalMethodRequest) {
    return this.post<IResponse<IJobAppraisalMethodModel>>(
      "OrgManagement/jobtitle/Create_JobTitleAppraisalMethod",
      data
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobAppraisalMethodModel>>(
      "OrgManagement/JobTitle/GetAll_JobTitleAppraisalMethod",
      {
        params: {
          JobId: jobId,
        },
      }
    );
  }
}
