import { Component, OnInit, Input } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { IJobDescriptionModel, IJobDescriptionRequest } from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { JobDescriptionService } from "app/org-management/services/job-profile/job-description.service";

@Component({
	selector: "app-job-description",
	templateUrl: "./job-description.component.html",
	styleUrls: ["./job-description.component.scss"],
})
export class JobDescriptionComponent extends AppHelpers implements OnInit {
	@Input() jobId: number;

	form: FormGroup;
	submitted: boolean = false;

	constructor(private jobDescriptionService: JobDescriptionService) {
		super();
	}

	ngOnInit() {
		this.form = this.fb.group({
			joB_PURPOSE: [null],
			responsibilitieS_DUTIES: [null],
			knowledgE_SKILLS: [null],
			education: [null],
			experience: [null],
			workinG_CONDITIONS: [null],
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) {
			if (this.jobId > 0) this.getAll();
		}
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
		this.showLoading();
		this.jobDescriptionService
			.getAllByJobTitleId(this.jobId)
			.subscribe((res: IResponse<IJobDescriptionModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.form.patchValue(res.data);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IJobDescriptionRequest = {
				job_Title_ID: this.jobId,
				joB_PURPOSE: this.form.value.joB_PURPOSE,
				responsibilitieS_DUTIES: this.form.value.responsibilitieS_DUTIES,
				knowledgE_SKILLS: this.form.value.knowledgE_SKILLS,
				education: this.form.value.education,
				experience: this.form.value.experience,
				workinG_CONDITIONS: this.form.value.workinG_CONDITIONS,
			};

			this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IJobDescriptionRequest) {
		this.showLoading();
		this.jobDescriptionService.create(model).subscribe((res: IResponse<IJobDescriptionModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				// this.getAll();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
