import { Injectable } from "@angular/core";
import {
  IJobCustodiesModel,
  IJobCustodiesRequest,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class JobCustodiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobCustodiesRequest) {
    return this.post<IResponse<IJobCustodiesModel>>(
      "OrgManagement/jobtitle/Create_JobTitleCustodies",
      data
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobCustodiesModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleCustodies",
      {
        params: {
          JobId: jobId,
        },
      }
    );
  }
}
