import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IOrganizationTypeModel,
  IOrganizationTypeRequest,
} from "app/org-management-setup/models/organization-types";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class OrganizationTypesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IOrganizationTypeRequest) {
    return this.post<IResponse<IOrganizationTypeModel>>("orgType/Create", data);
  }

  update(data: IOrganizationTypeRequest) {
    return this.post<IResponse<IOrganizationTypeModel>>("orgType/Edit", data);
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IOrganizationTypeModel[]>>(
      "OrgType/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IOrganizationTypeModel[]>>("OrgType/GetAll");
  }

  getAllInCurrentStructure() {
    return this.get<IResponse<IOrganizationTypeModel[]>>(
      "OrgType/GetAll_CurrentStructure"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("orgType/Delete", {
      params: { id: id },
    });
  }
}
