import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { S4SystemInstancePurposeComponent } from "app/s4-setup/components/s4-system-instance/s4-system-instance-purpose/s4-system-instance-purpose.component";
import { IS4SystemInstancePurposeModel } from "app/s4-setup/models/s4-system-instance";
import { S4SystemInstancePurposeService } from "app/s4-setup/services/s4-system-instance-purpose.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-s4-integration-purpose-by-system-instance-dropdown",
	templateUrl: "./s4-integration-purpose-by-system-instance-dropdown.component.html",
	styleUrls: ["./s4-integration-purpose-by-system-instance-dropdown.component.scss"],
})
export class S4IntegrationPurposeBySystemInstanceDropdownComponent
	extends AppHelpers
	implements OnInit
{
	@Input() control: FormControl;
	@Input() s4SystemInstanceControl: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "integrationPurpose";
	@Output() change = new EventEmitter<IS4SystemInstancePurposeModel[]>();

	integrationPurposeList: IS4SystemInstancePurposeModel[] = [];
	viewName: string;

	constructor(private s4SystemInstancePurposeService: S4SystemInstancePurposeService) {
		super();
		// add parent viewname beacuse it does  not have route

		this.viewName = ViewsManager.add_s4_system_instance;
	}

	ngOnInit(): void {
		this.control.disable();

		this.s4SystemInstanceControl?.valueChanges.subscribe((s4SystemInstanceId) => {
			this.control.setValue(null);

			if (s4SystemInstanceId > 0) {
				this.getAllIntegrationPurposesBySystemInstance();
				this.control.enable();
			} else this.control.disable();
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.s4SystemInstanceControl?.value > 0 && this.checkNoBusinessOrCompany())
			this.getAllIntegrationPurposesBySystemInstance();
	}

	getAllIntegrationPurposesBySystemInstance() {
		this.showLoading();
		this.s4SystemInstancePurposeService
			.getAllBySystemInstanceId(this.s4SystemInstanceControl.value)
			.subscribe((res: IResponse<IS4SystemInstancePurposeModel[]>) => {
				if ((res.message.messageType = MessageTypes.Success)) {
					this.integrationPurposeList = res.data;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	onChange(purpose: IS4SystemInstancePurposeModel[]) {
		this.change.emit(purpose);
	}
	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(
				S4SystemInstancePurposeComponent,
				value,
				this.s4SystemInstanceControl.value
			)
			.afterClosed()
			.subscribe((res) => {
				this.getAllIntegrationPurposesBySystemInstance();
			});
	}
}
