import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { AddUserComponent } from "app/general-setup/components/users/add-user/add-user.component";
import { IUMUsers } from "app/general-setup/models/users";
import { UsersService } from "app/general-setup/services/users/users.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-users-dropdown",
	templateUrl: "./users-dropdown.component.html",
	styleUrls: ["./users-dropdown.component.scss"],
})
export class UsersDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "user";
	viewName: string;

	usersList: IUMUsers[];

	constructor(private usersService: UsersService) {
		super();
		this.viewName = ViewsManager.add_user;
	}

	ngOnInit(): void {
		// if (this.businessId > 0 && this.companyId > 0) this.getAllUsers();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllUsers();
	}

	getAllUsers() {
		this.showLoading();
		this.usersService.GetAll().subscribe((res) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.usersList = res.data;
			} else this.errorList(res.message?.messages);
			this.hideLoading();
		});
	}
	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(AddUserComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllUsers();
			});
	}
}
