import { Injectable } from "@angular/core";
import {
  IOrganizationModel,
  IOrganizationRequest,
} from "app/org-management-setup/models/organizations";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class OrganizationsService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IOrganizationRequest) {
		return this.post<IResponse<IOrganizationModel>>("org/Create", data);
	}

	update(data: IOrganizationRequest) {
		return this.post<IResponse<IOrganizationModel>>("org/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IOrganizationModel[]>>("Org/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IOrganizationModel[]>>("Org/GetAll");
	}

	GetAllInCurrentStructureByOrgType(orgTypeId: number) {
		return this.get<IResponse<IOrganizationModel[]>>(
			"orgstructure/Get_CurrentOrganizationHierarchy",
			{
				params: {
					OrgType: orgTypeId,
					Level: 1,
				},
			}
		);
	}

	GetAllByOrgTypeAndStructure(orgTypeId: number, structureId: number) {
		return this.get<IResponse<IOrganizationModel[]>>("orgstructure/Get_OrganizationHierarchy", {
			params: {
				StructureId: structureId,
				OrgType: orgTypeId,
				Level: 1,
			},
		});
	}

  GetAllUnAssignedByStructureId(structureId: number) {
    return this.get<IResponse<IOrganizationModel[]>>(
      "Org/GetAll_NotAssignStructure",
      {
        params: {
          StructureID: structureId,
        },
      }
    );
  }

	Delete(Id: number) {
		return this.delete<IResponse>("Org/Delete", {
			params: { Id: Id },
		});
	}
}
