import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IElemenGroupModel } from "../models/element-group";
import { map } from "rxjs/operators";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class ElementGroupService extends AppClientService {
  constructor() {
    super();
  }

  GetAll() {
    return this.get<IResponse<IElemenGroupModel[]>>(
      "ElementGroupSetup/GetAll"
    ).pipe(
      map((res: IResponse<IElemenGroupModel[]>) => {
        res.data.map((item: IElemenGroupModel) => {
          item.ar_Name = item.ar_NAme;
        });
        return res;
      })
    );
  }
}
