import { Injectable } from "@angular/core";
import { INationalityModel, INationalityRequest } from "app/hr-setup/models/nationality";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
	providedIn: "root",
})
export class NationalityService extends AppClientService {
	constructor() {
		super();
	}

	create(data: INationalityRequest) {
		return this.post<IResponse<INationalityModel>>("HRSetup/Nationality/Create", data);
	}

	update(data: INationalityRequest) {
		return this.post<IResponse<INationalityModel>>("HRSetup/Nationality/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<INationalityModel[]>>("HRSetup/nationality/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<INationalityModel[]>>("HRSetup/Nationality/GetAll").pipe(
			map((res: IResponse<INationalityModel[]>) => {
				res.data.map((nationality: any) => {
					nationality.en_Name = nationality.en_NAme;
				});
				return res;
			})
		);
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/Nationality/Delete", {
			params: { Id: id },
		});
	}
}
