import { Component, Input, SimpleChanges, ViewChild } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IFileBase64, IFileBinary } from "app/shared/interfaces/files";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { environment } from "environments/environment";
import { FileUpload } from "primeng/fileupload";

interface UploadEvent {
  originalEvent: Event;
  files: File[];
}
@Component({
  selector: "app-file-upload",
  templateUrl: "./file-upload.component.html",
  styleUrls: ["./file-upload.component.scss"],
})
export class FileUploadComponent extends AppHelpers {
  @ViewChild("fileUpload", { static: false }) fileUpload: FileUpload;
  uploadedFiles: any[] = [];
  @Input() control: FormControl;
  @Input() multiple: boolean = true;
  @Input() url: string = null;
  @Input() accept: string = "";
  @Input() label: string = "";
  @Input() submitted: boolean = false;
  @Input() isHexImg: boolean = false;
  @Input() removeFiles: boolean = false;
  @Input() isEdit: boolean = false;

  baseUrl = `${environment.baseUrl}/api/`;

  onUpload(event: UploadEvent) {
    // console.log("control", this.control);
    for (let file of event.files) {
      this.uploadedFiles.push(file);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.removeFiles || changes.isEdit?.currentValue)
      this.fileUpload.clear();
  }

  // onChange(files) {
  //   if (this.isBase64) this.uploadBase64(files);
  //   else this.upload(files);
  // }

  // files with form submit
  public uploadFiles = (files) => {
    this.isEdit = false;
    this.control.setValue(this.fileService.upload(files));
  };

  async uploadBase64(files) {
    this.isEdit = false;
    await this.fileService.uploadBase64(files).then((file: IFileBase64) => {
      setTimeout(() => {
        this.control?.setValue(file);
      }, 100);
    });
  }

  async uploadHexImage(files) {
    this.isEdit = false;
    await this.fileService.uploadHexImage(files).then((file: IFileBinary) => {
      setTimeout(() => {
        this.control?.setValue(file.hexString);
      }, 100);
    });
  }

  removeFile() {
    // to edit >> in using multiple files
    if (!this.multiple) this.control?.setValue(null);
  }
}
