import { Component, Injector, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IFileBase64 } from "app/shared/interfaces/files";
import { FileService } from "app/shared/services/file.service";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-file-input",
  templateUrl: "./file-input.component.html",
  styleUrls: ["./file-input.component.scss"],
})
export class FileInputComponent extends AppHelpers implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() accept?: string;
  @Input() removeFile?: boolean;
  @Input() htmlId: string;
  @Input() label: string;
  @Input() isBase64: boolean = false;

  constructor(injector: Injector) {
    super();
  }

  ngOnInit(): void {}
  ngOnChanges(): void {
    if (this.removeFile) {
      this.fileService.resetFileById(this.htmlId);
    }
  }

  onChange(files) {
    if (this.isBase64) this.uploadBase64(files);
    else this.upload(files);
  }

  public upload = (files) => {
    this.control.setValue(this.fileService.upload(files));
  };

  async uploadBase64(event) {
    await this.fileService.uploadBase64(event).then((file: IFileBase64) => {
      // file to large > 10 .MB
      if (file.size > 10485760) {
        this.warning("General.largFileError");
        if (this.htmlId) this.fileService.resetFileById(this.htmlId);
      } else {
        setTimeout(() => {
          this.control?.setValue(file);
        }, 100);
      }
    });
  }
}
