import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IGenderModel } from "../models/gender";
import { map } from "rxjs/operators";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";

@Injectable({
	providedIn: "root",
})
export class GenderService extends AppClientService {
	constructor() {
		super();
	}

	getAll() {
		return this.get<IResponse<IGenderModel[]>>("HRSetup/Gender/GetAll").pipe(
			map((res: IResponse<IGenderModel[]>) => {
				res.data.map((item: any) => {
					item.en_Name = item.en_NAme;
				});
				return res;
			})
		);
	}
}
