import { Injectable } from "@angular/core";
import {
  IJobKpiModel,
  IJobKpiRequest,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class JobKpiService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobKpiRequest) {
    return this.post<IResponse<IJobKpiModel>>(
      "OrgManagement/jobtitle/Create_JobTitleKpi",
      data
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobKpiModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleKpi",
      {
        params: {
          JobId: jobId,
        },
      }
    );
  }
}
