import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

import { IResponse } from "app/shared/interfaces/responces";
import { IUMUsersGroup, IUserGroupsRequest } from "app/general-setup/models/users";

@Injectable({
	providedIn: "root",
})
export class UserGroupsService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IUserGroupsRequest[]) {
		return this.post<IResponse<IUMUsersGroup>>(
			"GeneralSetup/User/CreateUser_SecurityProfile",
			data
		);
	}

	replace(data: IUserGroupsRequest[]) {
		return this.post<IResponse<IUMUsersGroup>>(
			"GeneralSetup/User/ReplaceUser_SecurityProfile",
			data
		);
	}

	Delete(params) {
		return this.delete<IResponse>("GeneralSetup/User/DeleteUser_SecurityProfile", {
			params: params,
		});
	}
}
