import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { IFileBase64, IFileBinary } from "../interfaces/files";

@Injectable({
  providedIn: "root",
})
export class FileService {
  constructor() {}

  upload = (files) => {
    if (files.length == 0) return;
    else {
      const formData = new FormData();
      for (var i = 0; i < files.length; i++) {
        formData.append("files", files[i]);
        return formData;
      }
    }
  };

  download(url: string) {
    window.open(environment.baseUrl + url, "_blank");
  }

  preview(url: any) {
    if (url) {
      if (!url.FileAsBase64) return environment.baseUrl + url;
      else return url.FileAsBase64;
    }
  }

  resetFileInputs() {
    const FileInputs = document.querySelectorAll('input[type="file"]');
    FileInputs.forEach((input: any) => {
      input.value = null;
    });
  }

  resetFileById(htmlId: string) {
    console.log("htmlId", htmlId);
    if (htmlId != null)
      (<HTMLInputElement>document?.getElementById(htmlId)).value = null;
  }

  async uploadBase64(files: any) {
    let file = files[0];
    if (file) {
      let data = {} as IFileBase64;
      var reader = new FileReader();
      reader?.readAsDataURL(file);
      reader.onload = (event: any) => {
        data.FileAsBase64 = event.target.result; //replace("data:application/pdf;base64,","");
      };
      data.name = file.name;
      data.size = file.size;
      data.type = file.type;
      data.LastModified = file.lastModified;
      data.LastModifiedDate = file.lastModifiedDate;
      return await data;
    } else return null;
  }

  // deal with image as hexstring

  async uploadHexImage(files: File[]) {
    let file: File = files[0];
    if (file) {
      let data = {} as IFileBinary;
      var reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = (event: any) => {
        // result > binary data of the image
        data.binaryData = event.target.result;
        data.hexString = this.imgageToHex(data.binaryData);
        // console.log("Hexadecimal:", data.hexString);
      };
      return await data;
    } else return null;
  }

  private imgageToHex(buffer: ArrayBuffer): string {
    const uint8Array = new Uint8Array(buffer);
    let hexString = "";
    for (let i = 0; i < uint8Array.length; i++) {
      const hex = uint8Array[i].toString(16);
      hexString += hex.length === 1 ? "0" + hex : hex;
    }
    return hexString;
  }

  previewHexImage(hexString: string) {
    if (hexString) {
      let input = hexString.replace(/[^A-Fa-f0-9]/g, "");
      if (input.length % 2) {
        console.log("cleaned hex string length is odd.");
        return;
      }

      let binary = new Array();
      for (let i = 0; i < input.length / 2; i++) {
        let h = input.slice(i * 2, i * 2 + 2);
        binary[i] = parseInt(h, 16);
      }

      let base64String = "";
      for (let i = 0; i < binary.length; i++) {
        base64String += String.fromCharCode(binary[i]);
      }

      // btoa > convert binary string into base64
      return "data:image/png;base64," + btoa(base64String);
    }
  }
}
