import { Injectable } from "@angular/core";
import {
  IJobCareerPathDeleteRequest,
  IJobCareerPathModel,
  IJobCareerPathRequest,
  IJobTitleModel,
} from "app/org-management/model/job-title";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class JobCareerPathService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobCareerPathRequest) {
    return this.post<IResponse<IJobCareerPathModel>>(
      "OrgManagement/jobtitle/Create_JobTitleCareerPath",
      data
    );
  }

  getListByJobTitleId(jobId: number) {
    return this.get<IResponseList<IJobCareerPathModel[]>>(
      "OrgManagement/JobTitle/GetList_JobTitleCareerPath",
      {
        params: {
          $filter: `joB_Title_ID eq ${jobId}`,
          $expand: db.next_Job_Title,
        },
      }
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobCareerPathModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleCareerPath",
      {
        params: {
          $filter: `joB_Title_ID eq ${jobId}`,
          $expand: db.next_Job_Title,
        },
      }
    );
  }

  Delete(params: IJobCareerPathDeleteRequest) {
    return this.delete<IResponse>(
      "OrgManagement/jobtitle/Delete_JobTitleCareerPath",
      {
        params: params,
      }
    );
  }

  getAllNextJobTitles(jobId: number) {
    return this.get<IResponse<IJobTitleModel[]>>(
      "OrgManagement/JobTitle/GetAll_NextJobTitle",
      {
        params: {
          JobId: jobId,
        },
      }
    );
  }
}
