import { Component, Input, OnInit } from "@angular/core";
import { MatCheckboxChange } from "@angular/material/checkbox";
import { IGroupModel } from "app/general-setup/models/groups";
import { IUMUsersGroup, IUserGroupsRequest } from "app/general-setup/models/users";
import { GroupsService } from "app/general-setup/services/groups.service";
import { UserGroupsService } from "app/general-setup/services/users/user-groups.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-user-group",
	templateUrl: "./user-group.component.html",
	styleUrls: ["./user-group.component.scss"],
})
export class UserGroupComponent extends AppHelpers implements OnInit {
	@Input() userId: number;
	@Input() userGroupsIds: number[] = [];
	groupList: IGroupModel[];
	selectedGroupIds: number[] = [];
	constructor(private groupService: UserGroupsService, private _groupsService: GroupsService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany() && this.userId > 0) this.getAllGroups();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllGroups();
	}

	getAllGroups() {
		this.showLoading();
		this._groupsService.getAll().subscribe((res: IResponseList<IGroupModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.groupList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	submit() {
		if (this.userGroupsIds.length > 0) {
			const model: IUserGroupsRequest[] = this.userGroupsIds.map((groupID) => ({
				UserID: this.userId,
				GroupID: groupID,
			}));
			this.create(model);
		}
	}

	create(models: IUserGroupsRequest[]) {
		this.showLoading();
		this.groupService.replace(models).subscribe((res: IResponse<IUMUsersGroup>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res.message.messageTypeName);
			} else this.errorList(res.message.messages);
		});
	}

	selectGroup($event: MatCheckboxChange, group: IUMUsersGroup) {
		if ($event.checked) {
			this.userGroupsIds.push(group.id);
		} else {
			let index = this.userGroupsIds.findIndex((id) => id === group.id);
			this.userGroupsIds.splice(index, 1);
		}
	}
}
