import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IS4SystemInstanceModel,
  IS4SystemInstanceRequest,
} from "../models/s4-system-instance";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class S4SytemInstanceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IS4SystemInstanceRequest) {
    return this.post<IResponse<IS4SystemInstanceModel>>(
      "SystemInstanceS4Setup/Create",
      data
    );
  }

  update(data: IS4SystemInstanceRequest) {
    return this.post<IResponse<IS4SystemInstanceModel>>(
      "SystemInstanceS4Setup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IS4SystemInstanceModel[]>>(
      "SystemInstanceS4Setup/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IS4SystemInstanceModel[]>>(
      "SystemInstanceS4Setup/GetAll"
    );
  }

  getById(id: number) {
    return this.get<IResponse<IS4SystemInstanceModel[]>>(
      "SystemInstanceS4Setup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SystemInstanceS4Setup/Delete", {
      params: { Id: Id },
    });
  }
}
