import { Injectable } from '@angular/core';
import { IGroupPolicies } from 'app/general-setup/models/users';
import { IResponse } from '../../../shared/interfaces/responces';
import { AppClientService } from '../../../shared/services/app-client.service';

@Injectable({
  providedIn: 'root'
})
export class PoliceiesByCompanyService extends AppClientService {
	constructor() {
		super();
	}

  getPoliceiesByCompany(params){
    return this.get<IResponse<IGroupPolicies[]>>("OrgManagement/PolicyGroup/GetPoliceiesByCompany", {
      params: params
    });
  }
}
