import { Injectable } from "@angular/core";
import {
  IJobDescriptionModel,
  IJobDescriptionRequest,
} from "app/org-management/model/job-title";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class JobDescriptionService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IJobDescriptionRequest) {
    return this.post<IResponse<IJobDescriptionModel>>(
      "OrgManagement/jobtitle/Create_JobTitleDescription",
      data
    );
  }

  getAllByJobTitleId(jobId: number) {
    return this.get<IResponse<IJobDescriptionModel>>(
      "OrgManagement/JobTitle/GetAll_JobTitleDescription",
      {
        params: {
          JobId: jobId,
        },
      }
    );
  }
}
