import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";
import {
  IS4SystemInstancePurposeModel,
  IS4SystemInstancePurposeRequest,
} from "../models/s4-system-instance";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class S4SystemInstancePurposeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IS4SystemInstancePurposeRequest) {
    return this.post<IResponse<IS4SystemInstancePurposeModel>>(
      "SystemInstanceS4Setup/Create_Purpose_S4",
      data
    );
  }

  update(data: IS4SystemInstancePurposeRequest) {
    return this.post<IResponse<IS4SystemInstancePurposeModel>>(
      "SystemInstanceS4Setup/Edit_Purpose_S4",
      data
    );
  }

  getAllBySystemInstanceId(systemInstanceId: number) {
    return this.get<IResponse<IS4SystemInstancePurposeModel[]>>(
      "SystemInstanceS4Setup/GetAll_Purpose_S4",
      {
        params: {
          $filter: `SystemInstance_ID eq ${systemInstanceId}`,
        },
      }
    ).pipe(
      map((res) => {
        res.data.map((item: IS4SystemInstancePurposeModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SystemInstanceS4Setup/Delete_Purpose_S4", {
      params: { Id: Id },
    });
  }
}
