import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IEmployeeRequest,
  IEmployeeModel,
  IEmployeeCardModel,
} from "app/personnel-setup/models/employees";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class EmployeesService extends AppClientService {
  constructor() {
    super();
  }

  ////////// employees index
  getListCard(params: IPageParams) {
    return this.get<IResponseList<IEmployeeCardModel[]>>(
      "personnel/Employees/GetList_EmployeesCard",
      {
        params: params,
      }
    );
  }

  getAllCard() {
    return this.get<IResponse<IEmployeeCardModel[]>>(
      "Personnel/Employees/GetAll_EmployeesCard"
    );
  }

  getEmployeeCardById(employeeId: number) {
    return this.get<IResponse<IEmployeeCardModel[]>>(
      "Personnel/Employees/GetAll_EmployeesCard",
      {
        params: {
          $filter: `Person_Instance_ID eq ${employeeId}`,
        },
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IEmployeeModel[]>>(
      "Personnel/Employees/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IEmployeeModel[]>>("Personnel/Employees/GetAll");
  }

  getById(id: number) {
    return this.get<IResponse<IEmployeeModel[]>>("Personnel/Employees/GetAll", {
      params: {
        $filter: `id eq ${id}`,
      },
    });
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete", {
      params: { Id: Id },
    });
  }

  create(data: IEmployeeRequest) {
    return this.post<IResponse<IEmployeeRequest>>(
      "Personnel/Employees/Create",
      data
    );
  }

  update(data: IEmployeeRequest) {
    return this.post<IResponse<IEmployeeRequest>>(
      "Personnel/Employees/Edit",
      data
    );
  }

  // get all employees by structure and organization id

  getAllByStructureAndOrgId(structureId: number, orgId: number) {
    return this.get<IResponse<IEmployeeModel[]>>(
      "Personnel/Employees/GetAll_EmployeeByOrg",
      {
        params: {
          StructureId: structureId,
          Org_ID: orgId,
        },
      }
    );
  }
}
