import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { AppClientService } from "app/shared/services/app-client.service";
import { IGroupModel, IGroupRequest } from "../models/groups";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";

@Injectable({
	providedIn: "root",
})
export class GroupsService extends AppClientService {
	constructor() {
		super();
	}

  create(data: IGroupRequest) {
    return this.post<IResponse<IGroupModel>>(
      "GeneralSetup/UserGroup/Create",
      data
    );
  }

  update(data: IGroupRequest) {
    return this.post<IResponse<IGroupModel>>(
      "GeneralSetup/UserGroup/Edit",
      data
    );
  }

	getList(params: IPageParams) {
		return this.get<IResponseList<IGroupModel[]>>("GeneralSetup/UserGroup/GetList", {
			params: params,
		});
	}

  getGroupById(id : number) {
    return this.get<IResponse<IGroupModel[]>>(
      "GeneralSetup/UserGroup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IGroupModel[]>>(
      "GeneralSetup/UserGroup/GetAll"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("GeneralSetup/UserGroup/Delete", {
      params: { Id: id },
    });
  }
}
