import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IBankBranchRequest, IBankBranchModel } from "../../models/bank";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class BankBranchService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IBankBranchRequest) {
    return this.post<IResponse<IBankBranchModel>>(
      "HRSetup/bank/AddBankBranch",
      data
    );
  }

  update(data: IBankBranchRequest) {
    return this.post<IResponse<IBankBranchModel>>(
      "HRSetup/bank/EditBankBranch",
      data,

    );
  }

  getList(params) {
    return this.get<IResponseList<IBankBranchModel[]>>("HRSetup/bank/GetList", {
      params: params,
    });
  }

  getBankBranches(params, id: number) {
    return this.get<any>("HRSetup/Bank/GetAll", {
      params: {
        ...params,
        $filter: `id eq ${id}`,
        $expand: db.comm_branch,
      },
    });
  }

  getAll(params) {
    return this.get<IResponse<IBankBranchModel[]>>("HRSetup/Bank/GetAll", {
      params: params,
    });
  }

  Delete( id: number) {
    return this.delete<IResponse>("HRSetup/Bank/DeleteBankBranch", {
      params: {Id:id}
    });
  }
}
