import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IServiceModel } from "app/general-setup/models/service";
import { ServiceService } from "app/general-setup/services/service.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-service-dropdown",
	templateUrl: "./service-dropdown.component.html",
	styleUrls: ["./service-dropdown.component.scss"],
})
export class ServiceDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() filterId: boolean = false;
	@Input() label: string = "Service";
	servicesList: IServiceModel[];

	constructor(private serviceService: ServiceService) {
		super();
	}

	ngOnInit(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllService();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllService();
	}

	getAllService() {
		let params = {
			...this.businessCompanyParams,
			$filter: `id eq ${this.filterId}`,
		};
		this.showLoading();
		this.serviceService.GetAll(params).subscribe((res: IResponse<IServiceModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				res.data.map((item: IServiceModel) => {
					item.ar_Name = item.arbName;
					item.en_Name = item.engName;
				});
				this.servicesList = res.data;
			} else {
				this.errorList(res.message?.messages);
				this.hideLoading();
			}
		});
	}
}
